import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { AwardCategory } from 'src/app/shared/models/award-category.model';
import { Subject } from 'rxjs';
import { AwardCategoryService } from 'src/app/shared/services/award-category.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil } from 'rxjs/operators';
import { AwardTemplateService } from 'src/app/shared/services/award-template.service';
import { AwardTemplate } from 'src/app/shared/models/award-template.model';
import { AwardTemplateIds } from 'src/app/shared/models/award-template-ids.model';

@Component({
  selector: 'app-award-category-modal-form',
  templateUrl: './award-category-modal-form.component.html',
  styles: []
})
export class AwardCategoryModalFormComponent implements OnInit {

  @Input() modalData: AwardCategory
  @Input() create: boolean
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();
  public awardTemplates: AwardTemplate[];

  // Used in toggling controls
  public createOneDisabled: boolean = false;
  public createDefaultTemplateDisabled: boolean = false;
  public createSelectTemplateDisabled: boolean = false;

  // Used in toggling save mode
  public createOne: boolean = false;
  public createDefaultTemplate = false;
  public createSelectTemplate = false;

  // TODO: change this to actual modalData
  public selectedTemplate: any;

  constructor(
    public service: AwardCategoryService,
    public awardTemplateService: AwardTemplateService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
    this.getAwardTemplates();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        AwardCategoryId: 0,
        AwardId: 0,
        AwardTemplateId: 0,
        AwardCategoryDescription: '',
        AwardDocumentations: []
      }
    }
  }

  getAwardTemplates() {
    this.awardTemplateService.getAwardTemplatesFiltered()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: AwardTemplate[]) => {
        this.awardTemplates = response;
      });
  }

  onSubmit() {
    if (!this.create || this.createOne) {
      if (this.modalData.AwardCategoryId == 0) {
        this.insertRecord(this.modalData);
      } else {
        this.updateRecord(this.modalData);
      }
    }
    if (this.createDefaultTemplate) {
      var ids = new AwardTemplateIds();
      ids.AwardId = this.modalData.AwardId;
      ids.AwardTemplateId = 0;
      this.insertRecordBatch(ids);
    }
    if (this.createSelectTemplate) {
      var ids = new AwardTemplateIds();
      ids.AwardId = this.modalData.AwardId;
      ids.AwardTemplateId = this.modalData.AwardTemplateId;
      this.insertRecordBatch(ids);
    }
  }

  insertRecordBatch(awardTemplateIds: AwardTemplateIds) {
    this.awardTemplateService.postAwardTemplateBatch(awardTemplateIds)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response) => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Award Category Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      })
  }

  insertRecord(model: AwardCategory) {
    this.service.formData = model;
    this.service.postAwardCategory()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Award Category Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(model: AwardCategory) {
    this.service.formData = model;
    this.service.putAwardCategory()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Updated Successfully', 'Award Category Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  closeModal() {
    this.activeModal.close();
  }

  onChangeDefaultTemplate(e) {
    if (e.target.checked) {
      // disables the input and dropdown control
      this.createOneDisabled = true;
      this.createSelectTemplateDisabled = true;

      // tags the create from default when saving
      this.createOne = false;
      this.createDefaultTemplate = true;
      this.createSelectTemplate = false;
    } else {
      // enables the input and dropdown control
      this.createOneDisabled = false;
      this.createSelectTemplateDisabled = false;
    }
  }

  onChangeCreateOne() {
    if (this.create) {
      // Execute this on create mode only
      if (this.modalData.AwardCategoryDescription != '') {
        // disables the dropdown and checkbox control
        this.createSelectTemplateDisabled = true;
        this.createDefaultTemplateDisabled = true;

        // tags the create one when saving
        this.createOne = true;
        this.createDefaultTemplate = false;
        this.createSelectTemplate = false;
      } else {
        // enables dropdown and checkbox control
        this.createSelectTemplateDisabled = false;
        this.createDefaultTemplateDisabled = false;
      }
    } 
  }

  onChangeSelectTemplate() {
    if (this.selectedTemplate != 0) {
      // disables the input and checkbox control
      this.createOneDisabled = true;
      this.createDefaultTemplateDisabled = true;

      // tags the create from selected template when saving
      this.createOne = false;
      this.createDefaultTemplate = false;
      this.createSelectTemplate = true;
    } else {
      // enables the input and checkbox control
      this.createOneDisabled = false;
      this.createDefaultTemplateDisabled = false;
    }
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
