import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Award } from 'src/app/shared/models/award.model';
import { SubgranteeDocumentation } from 'src/app/shared/models/subgrantee-documentation.model';
import { Subject } from 'rxjs';
import { SubgranteeDocumentationService } from 'src/app/shared/services/subgrantee-documentation.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil } from 'rxjs/operators';
import { DocumentType } from 'src/app/shared/models/document-type.model';
import { Status } from 'src/app/shared/models/status.model';
import { DocumentTypeLookupComponent } from 'src/app/lookups/document-type-lookup/document-type-lookup.component';
import { StatusLookupComponent } from 'src/app/lookups/status-lookup/status-lookup.component';

@Component({
  selector: 'app-subgrantee-documentation-modal-form',
  templateUrl: './subgrantee-documentation-modal-form.component.html',
  styles: []
})
export class SubgranteeDocumentationModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: SubgranteeDocumentation;
  @Input() award: Award;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeDocumentationService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        SubGranteeDocumentationId: 0,
        SubGranteeCategoryId: 0,
        DocumentTypeId: 0,
        StartDate: null,
        EndDate: null,
        DueDate: null,
        StatusId: 0,
        AwardNumber: '',
        DocumentTypeDescription: '',
        StatusDescription: '',
        SubGranteeDocumentationAttachments: []
      }
    }
  }

  onSubmit() {
    if (this.modalData.SubGranteeDocumentationId == 0) {
      this.insertRecord(this.modalData)
    } else {
      this.updateRecord(this.modalData);
    }
  }

  insertRecord(model: SubgranteeDocumentation) {
    this.service.formData = model;
    this.service.postSubGranteeDocumentation()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Sub Grantee Documentation');
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(model: SubgranteeDocumentation) {
    this.service.formData = model;
    this.service.putSubGranteeDocumentation()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm()
          this.toastr.success('Updated Successfully', 'Sub Grantee Documentation');
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  launchLookup(lookupType) {
    if (lookupType == 'document-type') {
      let modalRef = this.modal.open(DocumentTypeLookupComponent);

      // Subscribe to the modal event emitter and wait for the response
      modalRef.componentInstance.notifyParent.subscribe((response: DocumentType) => {
        this.modalData.DocumentTypeId = response.DocumentTypeId;
        this.modalData.DocumentTypeDescription = response.DocumentTypeDescription;
      });
    }
    if (lookupType == 'status') {
      let modalRef = this.modal.open(StatusLookupComponent);

      // Subscribe to the modal event emitter and wait for the response
      modalRef.componentInstance.notifyParent.subscribe((response: Status) => {
        this.modalData.StatusId = response.StatusId;
        this.modalData.StatusDescription = response.StatusDescription;
      });
    }
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
