import { Component, OnInit, OnDestroy, ViewChild, ElementRef } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AwardService } from 'src/app/shared/services/award.service';
import { Award } from 'src/app/shared/models/award.model';
import { DonorLookupComponent } from 'src/app/lookups/donor-lookup/donor-lookup.component';
import { OfficeMissionLookupComponent } from 'src/app/lookups/office-mission-lookup/office-mission-lookup.component';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Donor } from 'src/app/shared/models/donor.model';
import { Officemission } from 'src/app/shared/models/officemission.model';
import { ToastrService } from 'ngx-toastr';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

function captureTab() {
  var txtbox = document.getElementById('AwardNumber');
  txtbox.focus();
}

@Component({
  selector: 'app-award-data-entry',
  templateUrl: './award-data-entry.component.html',
  styles: []
})
export class AwardDataEntryComponent implements OnInit, OnDestroy {

  public awardId: number = 0;
  public award: Award;
  public showAwardCategory: boolean = false;

  public ngUnsubscribe: Subject<any> = new Subject();

  originalAwardNumber: string = '';

  //@ViewChild("AwardName") awardNameField: ElementRef;

  constructor(
    public route: ActivatedRoute,
    public router: Router,
    public service: AwardService,
    public modal: NgbModal,
    public toastr: ToastrService
  ) {
    this.getRouteParam();
  }

  ngOnInit() {
    this.initializeVariables();
  }

  initializeVariables() {
    this.award = {
      AwardId: 0,
      AwardNumber: '',
      AwardName: '',
      DonorId: 0,
      OfficeMissionId: 0,
      ProgramTitle: '',
      AwardContact: '',
      OrganizationProjectManager: '',
      StartDate: null,
      EndDate: null,
      AwardAmount: null,
      ProposedAwardAmount: null,
      CeilingAmount: null,
      ObligatedAmount: null,
      CostShareAmount: null,
      AwardStatus: 1,
      SelectedStartDate: null,
      SelectedEndDate: null,
      DonorName: '',
      OfficeMissionName: ''
    }
  }

  getRouteParam() {
    this.route.paramMap
      .subscribe(params => {
        if (+params.get('id') != 0) {
          this.awardId = +params.get('id');
          this.getAward();
          this.showAwardCategory = true;
        } 
      });
  }

  getAward() {
    this.service.getAward(this.awardId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Award) => {
        this.award = response;
      });
  }

  onChange(confirmModal) {
    if (this.award.AwardNumber != this.originalAwardNumber) {
      this.getAwardByAwardNumber(confirmModal);
    }
  }

  getAwardByAwardNumber(confirmModal) {
    this.service.getAwardByAwardNumber(this.award.AwardNumber)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Award) => {
        if (response != null) {
          captureTab(); // <-- Hack!!!
          this.modal.open(confirmModal).result.then(result => {
            this.award = response;
            this.originalAwardNumber = response.AwardNumber;
            this.redirect(response.AwardId);
          }, reason => {
            this.award.AwardNumber = this.originalAwardNumber;
          });
        }
      });
  }

  marAsUntouched(event) {
    event.defaulPrevented = true;
  }

  redirect(id) {
    this.router.navigate(['/award-data-entry/' + id]);
  }

  launchLookup(lookupType) {
    if (lookupType == 'donor') {
      let modalRef = this.modal.open(DonorLookupComponent);

      // Subscribe to the modal event emitter and wait for the response
      modalRef.componentInstance.notifyParent.subscribe((response: Donor) => {
        this.award.DonorId = response.DonorId;
        this.award.DonorName = response.DonorName;
      });
    }
    if (lookupType == 'officemission') {
      let modalRef = this.modal.open(OfficeMissionLookupComponent);

      // Subscribe to the modal event emitter and wait for the response
      modalRef.componentInstance.notifyParent.subscribe((response: Officemission) => {
        this.award.OfficeMissionId = response.OfficeMissionId;
        this.award.OfficeMissionName = response.OfficeMissionName;
      });
    }
  }

  onSave() {
    if (this.award.AwardId == 0) {
      this.insertRecord(this.award)
    } else {
      this.updateRecord(this.award);
    }
  }

  insertRecord(a: Award) {
    this.service.formData = a;
    this.service.postAward()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      (response: Award) => {
        this.toastr.success('Submitted Successfully', 'Award Register');
        this.redirect(response.AwardId);
      },
      err => {
        console.log(err);
      }
    )
  }

  updateRecord(a: Award) {
    this.service.formData = a;
    this.service.putAward()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      response => { 
        this.toastr.info('Updated Successfully', 'Award Register');
      },
      err => {
        console.log(err);
      }
    )
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
