import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { AwardDocumentationAttachmentService } from 'src/app/shared/services/award-documentation-attachment.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { HttpEventType } from '@angular/common/http';
import { ToastrService } from 'ngx-toastr';
import { AwardDocumentation } from 'src/app/shared/models/award-documentation.model';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-award-documentation-attachment-modal-form',
  templateUrl: './award-documentation-attachment-modal-form.component.html',
  styles: []
})
export class AwardDocumentationAttachmentModalFormComponent implements OnInit, OnDestroy {

  @Input() awardDocumentation: AwardDocumentation;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public progress: number;
  public message: string = '';
  public showDiv: boolean = false;

  constructor(
    public service: AwardDocumentationAttachmentService,
    public activeModal: NgbActiveModal,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    
  }

  /**
   * Subscribes to the observable method (upload) from AwardDocumentationAttachmentService
   * @param: files (from the fileupload control)
   * */
  upload(files) {
    this.service.upload(files, this.awardDocumentation.AwardDocumentationId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(event => {

          if (event.type === HttpEventType.UploadProgress)
            this.progress = Math.round(100 * event.loaded / event.total);
          else if (event.type === HttpEventType.Response)
            this.message = event.body.toString();

        // Notify parent to refresh the grid
        this.notifyParent.emit('Reload');

      }, error => {
          console.error(error);
        }, () => {
          this.activeModal.close();
          this.toastr.success("File successfully uploaded!", "File Upload");
        }
      )
  }

  /**
   * Closes the active modal
   * @param: None
   * */
  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }


}
