import { Component, OnInit, OnDestroy } from '@angular/core';
import { OfficemissionService } from 'src/app/shared/services/officemission.service';
import { ToastrService } from 'ngx-toastr';
import { Officemission } from 'src/app/shared/models/officemission.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { OfficemissionModalFormComponent } from '../officemission-modal-form/officemission-modal-form.component';
import { Observable, Subject } from 'rxjs';
import { map, startWith, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';


@Component({
  selector: 'app-officemission-list',
  templateUrl: './officemission-list.component.html',
  styles: []
})
export class OfficemissionListComponent implements OnInit, OnDestroy {

  public officeMissionArray: Officemission[];
  public officeMissionArrayObservable: Observable<Officemission[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: OfficemissionService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getOfficeMissions();
  }

  /**
   * Searches the officeMissionArray
   * @param: text
   */
  search(text: string): Officemission[] {
    return this.officeMissionArray.filter(officeMissions => {
      const term = text.toLowerCase();
      return officeMissions.OfficeMissionName.toLowerCase().includes(term);
    });
  }

  getOfficeMissions() {
    this.service.getOfficeMissions()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Officemission[]) => {
        this.officeMissionArray = response;
      }, err => {
        console.log(err);
      },
      () => {
        this.officeMissionArrayObservable = this.filter.valueChanges.pipe(
          startWith(''),
          map(text => this.search(text))
        );
      });
  }

  onDelete(OfficeMissionId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteOfficeMission(OfficeMissionId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getOfficeMissions();
            this.toastr.success('Deleted Successfullly', 'Office Mission Registration');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(om?: Officemission) {
    let modalRef = this.modal.open(OfficemissionModalFormComponent);

    if (om != null) {
      modalRef.componentInstance.modalData = Object.assign({}, om);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getOfficeMissions();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
