import { Component, OnInit, OnDestroy } from '@angular/core';
import { Opportunity } from 'src/app/shared/models/opportunity.model';
import { Subject, Observable } from 'rxjs';
import { OpportunitySubgranteeService } from 'src/app/shared/services/opportunity-subgrantee.service';
import { ActivatedRoute, Router } from '@angular/router';
import { OpportunitySubgrantee } from 'src/app/shared/models/opportunity-subgrantee.model';
import { FormControl } from '@angular/forms';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { OpportunityService } from 'src/app/shared/services/opportunity.service';

@Component({
  selector: 'app-opportunity-subgrantee-list',
  templateUrl: './opportunity-subgrantee-list.component.html',
  styles: []
})
export class OpportunitySubgranteeListComponent implements OnInit, OnDestroy {

  public opportunityId: number = 0;
  public opportunity: Opportunity;
  public opportunitySubGranteeArray: OpportunitySubgrantee[];
  public opportunitySubGranteeArrayObservable: Observable<OpportunitySubgrantee[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public route: ActivatedRoute,
    public router: Router,
    public service: OpportunitySubgranteeService,
    public opportunityService: OpportunityService,
    public modal: NgbModal,
    public toastr: ToastrService
  ) {
    this.getRouteParam();
  }

  ngOnInit() {
    this.initializeVariables();
  }

  initializeVariables() {
    this.opportunity = {
      RFPId: 0,
      RFPName: '',
      AwardId: 0,
      DeadlineDate: null,
      DecisionDate: null,
      AwardNumber: ''
    }
  }

  getRouteParam() {
    this.route.paramMap
      .subscribe(params => {
        if (+params.get('id') != 0) {
          this.opportunityId = +params.get('id');
          this.getOpportunity();
          this.getOpportunitySubgrantees();
        }
      });
  }

  /**
   * Searches the donorTypeArray
   * @param: text
   */
  search(text: string): OpportunitySubgrantee[] {
    return this.opportunitySubGranteeArray.filter(opportunitySubGrantees => {
      const term = text.toLowerCase();
      return  opportunitySubGrantees.RFPName.toLowerCase().includes(term) ||
              opportunitySubGrantees.SubGranteeName.toLowerCase().includes(term) ||
              opportunitySubGrantees.SubGranteeTypeDescription.toLowerCase().includes(term);
    });
  }

  getOpportunitySubgrantees() {
    this.service.getOpportunitySubgranteesFiltered(this.opportunityId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: OpportunitySubgrantee[]) => {
        this.opportunitySubGranteeArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.opportunitySubGranteeArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  getOpportunity() {
    this.opportunityService.getOpportunity(this.opportunityId)
      .subscribe((response: Opportunity) => {
        this.opportunity = response;
      });
  }

  onDelete(OpportunitySubGranteeId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteOpportunitySubgrantee(OpportunitySubGranteeId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getOpportunitySubgrantees();
            this.toastr.success('Deleted Successfullly', 'Opportunity Sub Grantee Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
