import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { SubgranteeDocumentation } from 'src/app/shared/models/subgrantee-documentation.model';
import { SubgranteeDocumentationAttachment } from 'src/app/shared/models/subgrantee-documentation-attachment.model';
import { FormControl } from '@angular/forms';
import { SubgranteeDocumentationAttachmentService } from 'src/app/shared/services/subgrantee-documentation-attachment.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { SubgranteeDocumentationAttachmentModalFormComponent } from '../subgrantee-documentation-attachment-modal-form/subgrantee-documentation-attachment-modal-form.component';

@Component({
  selector: 'app-subgrantee-documentation-attachment-list',
  templateUrl: './subgrantee-documentation-attachment-list.component.html',
  styles: []
})
export class SubgranteeDocumentationAttachmentListComponent implements OnInit, OnDestroy {

  @Input() subgranteeDocumentation: SubgranteeDocumentation;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public subGranteeDocumentationAttachmentArray: SubgranteeDocumentationAttachment[];
  public subGranteeDocumentationAttachmentArrayObservable: Observable<SubgranteeDocumentationAttachment[]>;
  public filter = new FormControl('');

  constructor(
    public service: SubgranteeDocumentationAttachmentService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  /**
   * Searches the donorArray
   * @param: text
   */
  search(text: string): SubgranteeDocumentationAttachment[] {
    return this.subGranteeDocumentationAttachmentArray.filter(att => {
      const term = text.toLowerCase();
      return att.FileName.toLowerCase().includes(term);
    });
  }

  ngOnInit() {
    this.getSubGranteeDocumentationAttachments();
  }

  getSubGranteeDocumentationAttachments() {
    this.service.getSubGranteeDocumentationAttachments(this.subgranteeDocumentation.SubGranteeDocumentationId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: SubgranteeDocumentationAttachment[]) => {
        this.subGranteeDocumentationAttachmentArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.subGranteeDocumentationAttachmentArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(SubGranteeDocumentationAttachmentId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteAttachment(SubGranteeDocumentationAttachmentId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getSubGranteeDocumentationAttachments();
            this.toastr.success('Deleted Successfullly', 'Sub Grantee Documentation Attachment Registration');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  download(id) {
    this.service.download(id);
  }

  showFileUpload() {
    let modalRef = this.modal.open(SubgranteeDocumentationAttachmentModalFormComponent);
    modalRef.componentInstance.subgranteeDocumentation = this.subgranteeDocumentation;

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getSubGranteeDocumentationAttachments();
      }
    });
  }

  closeModal() {
    this.notifyParent.emit('Reload');
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
