import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Opportunity } from 'src/app/shared/models/opportunity.model';
import { OpportunityService } from 'src/app/shared/services/opportunity.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { NgForm } from '@angular/forms';
import { AwardService } from 'src/app/shared/services/award.service';
import { Award } from 'src/app/shared/models/award.model';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-opportunity-modal-form',
  templateUrl: './opportunity-modal-form.component.html',
  styles: []
})
export class OpportunityModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: Opportunity;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public awards: Award[];

  constructor(
    public service: OpportunityService,
    public awardService: AwardService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();

    // For dropdown list
    this.getAwards();
  }

  getAwards() {
    this.awardService.refreshList()
      .subscribe((response: Award[]) => {
        this.awards = response;
      });
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        RFPId: 0,
        RFPName: '',
        AwardId: 0,
        AwardNumber: '',
        DeadlineDate: null,
        DecisionDate: null
      }
    }
  }

  onSubmit() {
    if (this.modalData.RFPId == 0) {
      this.insertRecord(this.modalData)
    } else {
      this.updateRecord(this.modalData);
    }
  }

  insertRecord(model: Opportunity) {
    this.service.formData = model;
    this.service.postOpportunity()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Opportunity');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  updateRecord(model: Opportunity) {
    this.service.formData = model;
    this.service.putOpportunity()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm()
        this.toastr.success('Updated Successfully', 'Opportunirt');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
