import { Injectable } from '@angular/core';
import { AwardDocumentationTemplate } from '../models/award-documentation-template.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class AwardDocumentationTemplateService {

  readonly rootURL = this.configService.config['api_root'];

  formData: AwardDocumentationTemplate;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postAwardDocumentationTemplate() {
    return this.http.post(this.rootURL + '/AwardDocumentationTemplate', this.formData);
  }

  postBatchAwardDocumentationTemplate(awardDocumentationTemplates: AwardDocumentationTemplate[]) {
    return this.http.post(this.rootURL + '/AwardDocumentationTemplate/Batch', awardDocumentationTemplates);
  }

  putAwardDocumentationTemplate() {
    return this.http.put(this.rootURL + '/AwardDocumentationTemplate/' + this.formData.AwardCategoryTemplateId, this.formData);
  }

  deleteAwardDocumentationTemplate(id) {
    return this.http.delete(this.rootURL + '/AwardDocumentationTemplate/' + id);
  }

  deleteAwardDocumentationTemplatesBatch(id) {
    return this.http.delete(this.rootURL + '/AwardDocumentationTemplate/Batch/' + id);
  }

  getAwardDocumentationTemplates(id) {
    return this.http.get(this.rootURL + '/AwardDocumentationTemplate/filter/' + id);
  }
}
