import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { OpportunitySubgrantee } from 'src/app/shared/models/opportunity-subgrantee.model';
import { SubgranteeCategory } from 'src/app/shared/models/subgrantee-category.model';
import { Subject } from 'rxjs';
import { SubgranteeCategoryService } from 'src/app/shared/services/subgrantee-category.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { SubgranteeDocumentationService } from 'src/app/shared/services/subgrantee-documentation.service';
import { takeUntil } from 'rxjs/operators';
import { SubgranteeCategoryModalFormComponent } from '../subgrantee-category-modal-form/subgrantee-category-modal-form.component';
import { SubgranteeDocumentation } from 'src/app/shared/models/subgrantee-documentation.model';
import { SubgranteeDocumentationModalFormComponent } from 'src/app/subgrantee-documentation/subgrantee-documentation-modal-form/subgrantee-documentation-modal-form.component';
import { SubgranteeDocumentationAttachmentListComponent } from 'src/app/subgrantee-documentation-attachment/subgrantee-documentation-attachment-list/subgrantee-documentation-attachment-list.component';

@Component({
  selector: 'app-subgrantee-category-tab',
  templateUrl: './subgrantee-category-tab.component.html',
  styles: []
})
export class SubgranteeCategoryTabComponent implements OnInit, OnDestroy {

  @Input('fromParent') opportunitySubGrantee: OpportunitySubgrantee;

  public subGranteeCategories: SubgranteeCategory[];
  public attachmentCount: number = 0;

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeCategoryService,
    public subGranteeDocumentationService: SubgranteeDocumentationService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getSubGranteeCategories()
  }

  getSubGranteeCategories() {
    this.service.getSubGranteeCategories(this.opportunitySubGrantee.OpportunitySubGranteeId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: SubgranteeCategory[]) => {
        this.subGranteeCategories = response;
      });
  }

  showDataEntry(sgc?: SubgranteeCategory) {
    let modalRef = this.modal.open(SubgranteeCategoryModalFormComponent, { backdrop: 'static' });

    if (sgc != null) {
      modalRef.componentInstance.create = false;
      modalRef.componentInstance.modalData = Object.assign({}, sgc);
    } else {
      sgc = new SubgranteeCategory();
      sgc.SubGranteeCategoryId = 0;
      sgc.OpportunitySubGranteeId = this.opportunitySubGrantee.OpportunitySubGranteeId;
      sgc.SubGranteeCategoryDescription = '';

      modalRef.componentInstance.create = true;
      modalRef.componentInstance.modalData = Object.assign({}, sgc);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getSubGranteeCategories();
      }
    });
  }

  onDelete(SubGranteeCategoryId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteSubGranteeCategory(SubGranteeCategoryId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getSubGranteeCategories();
            this.toastr.success('Deleted Successfullly', 'Sub Grantee Documentation');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showSubGranteedDocumentationDataEntry(subGranteeCategoryId: number, sd?: SubgranteeDocumentation) {
    let modalRef = this.modal.open(SubgranteeDocumentationModalFormComponent);

    if (sd != null) {
      modalRef.componentInstance.modalData = Object.assign({}, sd);
    } else {
      sd = new SubgranteeDocumentation();
      sd.SubGranteeDocumentationId = 0;
      sd.SubGranteeCategoryId = subGranteeCategoryId;
      sd.DocumentTypeId = 0;
      sd.StartDate = null;
      sd.EndDate = null;
      sd.DueDate = null;
      sd.StatusId = 0;

      modalRef.componentInstance.modalData = Object.assign({}, sd);
    }
    modalRef.componentInstance.award = Object.assign({}, this.opportunitySubGrantee);

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getSubGranteeCategories();
      }
    });
  }

  onDeleteSubGranteeDocumentation(SubGranteeDocumentationId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.subGranteeDocumentationService.deleteSubGranteeDocumentation(SubGranteeDocumentationId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getSubGranteeCategories();
            this.toastr.success('Deleted Successfullly', 'Sub Grantee Documentation');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showSubGranteeDocumentationAttachmentList(sg: SubgranteeDocumentation) {
    let modalRef = this.modal.open(SubgranteeDocumentationAttachmentListComponent, { size: 'lg', backdrop: 'static' });

    if (sg != null) {
      modalRef.componentInstance.subgranteeDocumentation = Object.assign({}, sg);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getSubGranteeCategories();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
