import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { AwardTemplate } from 'src/app/shared/models/award-template.model';
import { Subject } from 'rxjs';
import { AwardTemplateService } from 'src/app/shared/services/award-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-award-template-modal-form',
  templateUrl: './award-template-modal-form.component.html',
  styles: []
})
export class AwardTemplateModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: AwardTemplate
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: AwardTemplateService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        AwardTemplateId: 0,
        AwardTemplateDescription: '',
        IsDefault: false,
        AwardId: 0,
        AwardCategoryTemplates: []
      }
    }
  }

  onSubmit() {
    if (this.modalData.AwardTemplateId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
    //this.activeModal.close();
  }

  insertRecord(model: AwardTemplate) {
    this.service.formData = model;
    this.service.postAwardTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Award Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(model: AwardTemplate) {
    this.service.formData = model;
    this.service.putAwardTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Updated Successfully', 'Award Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
