import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { Status } from 'src/app/shared/models/status.model';
import { StatusService } from 'src/app/shared/services/status.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-status-modal-form',
  templateUrl: './status-modal-form.component.html',
  styles: []
})
export class StatusModalFormComponent implements OnInit {

  @Input() modalData: Status
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  constructor(
    public service: StatusService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        StatusId: 0,
        StatusDescription: ''
      }
    }
  }

  onSubmit() {
    if (this.modalData.StatusId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
    this.activeModal.close();
  }

  insertRecord(model: Status) {
    this.service.formData = model;
    this.service.postStatus().subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Status Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
      },
      err => {
        console.log(err);
      }
    )
  }

  updateRecord(model: Status) {
    this.service.formData = model;
    this.service.putStatus().subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Updated Successfully', 'Status Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
      },
      err => {
        console.log(err);
      }
    )
  }

  closeModal() {
    this.activeModal.close();
  }
}
