import { Component, OnInit } from '@angular/core';
import { Status } from 'src/app/shared/models/status.model';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { FormControl } from '@angular/forms';
import { StatusService } from 'src/app/shared/services/status.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { StatusModalFormComponent } from '../status-modal-form/status-modal-form.component';

@Component({
  selector: 'app-status-list',
  templateUrl: './status-list.component.html',
  styles: []
})
export class StatusListComponent implements OnInit {

  public statusArray: Status[];
  public statusArrayObservable: Observable<Status[]>;
  public filter = new FormControl('');

  constructor(
    public service: StatusService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getStatuses();
  }

  /**
   * Searches the statusArray
   * @param: text
   */
  search(text: string): Status[] {
    return this.statusArray.filter(statuses => {
      const term = text.toLowerCase();
      return statuses.StatusDescription.toLowerCase().includes(term);
    });
  }

  getStatuses() {
    this.service.getStatuses()
      .subscribe((response: Status[]) => {
        this.statusArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.statusArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(StatusId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteStatus(StatusId)
        .subscribe(
          res => {
            this.getStatuses();
            this.toastr.success('Deleted Successfullly', 'Status Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(s?: Status) {
    let modalRef = this.modal.open(StatusModalFormComponent);

    if (s != null) {
      modalRef.componentInstance.modalData = Object.assign({}, s);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getStatuses();
      }
    });
  }

}
