import { Component, OnInit, OnDestroy } from '@angular/core';
import { SubgranteeTemplate } from 'src/app/shared/models/subgrantee-template.model';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { SubgranteeTemplateService } from 'src/app/shared/services/subgrantee-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { SubgranteeTemplateModalFormComponent } from '../subgrantee-template-modal-form/subgrantee-template-modal-form.component';
import { SubgranteeCategoryTemplateListComponent } from 'src/app/subgrantee-category-template/subgrantee-category-template-list/subgrantee-category-template-list.component';

@Component({
  selector: 'app-subgrantee-template-list',
  templateUrl: './subgrantee-template-list.component.html',
  styles: []
})
export class SubgranteeTemplateListComponent implements OnInit, OnDestroy {

  public subgranteeTemplateArray: SubgranteeTemplate[];
  public subgranteeTemplateArrayObservable: Observable<SubgranteeTemplate[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeTemplateService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getSubGranteeTemplates();
  }

  /**
   * Searches the awardTemplateArray
   * @param: text
   */
  search(text: string): SubgranteeTemplate[] {
    return this.subgranteeTemplateArray.filter(subgranteeTemplates => {
      const term = text.toLowerCase();
      return subgranteeTemplates.SubGranteeTemplateDescription.toLowerCase().includes(term);
    });
  }

  getSubGranteeTemplates() {
    this.service.getSubGranteeTemplates()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: SubgranteeTemplate[]) => {
        this.subgranteeTemplateArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.subgranteeTemplateArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(SubGranteeTemplateId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteSubGranteeTemplate(SubGranteeTemplateId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getSubGranteeTemplates()
            this.toastr.success('Deleted Successfullly', 'Sub Grantee Template Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(st?: SubgranteeTemplate) {
    let modalRef = this.modal.open(SubgranteeTemplateModalFormComponent);

    if (st != null) {
      modalRef.componentInstance.modalData = Object.assign({}, st);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getSubGranteeTemplates();
      }
    });
  }

  showSubGranteeCategoryTemplate(st: SubgranteeTemplate) {
    let modalRef = this.modal.open(SubgranteeCategoryTemplateListComponent, { size: 'lg', backdrop: 'static' });

    modalRef.componentInstance.subgranteeTemplate = Object.assign({}, st);
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
