import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Officemission } from '../models/officemission.model';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class OfficemissionService {
  readonly rootURL = this.configService.config['api_root'];

  formData: Officemission;
  list: Officemission[];

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postOfficeMission() {
    return this.http.post(this.rootURL + '/OfficeMission', this.formData);
  }

  putOfficeMission() {
    return this.http.put(this.rootURL + '/OfficeMission/' + this.formData.OfficeMissionId, this.formData);
  }

  deleteOfficeMission(id) {
    return this.http.delete(this.rootURL + '/OfficeMission/' + id);
  }

  getOfficeMissions() {
    return this.http.get(this.rootURL + '/OfficeMission');
  }
}
