import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { SubgranteeTemplate } from 'src/app/shared/models/subgrantee-template.model';
import { SubgranteeCategoryTemplate } from 'src/app/shared/models/subgrantee-category-template.model';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { SubgranteeCategoryTemplateService } from 'src/app/shared/services/subgrantee-category-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { SubgranteeCategoryTemplateModalFormComponent } from '../subgrantee-category-template-modal-form/subgrantee-category-template-modal-form.component';

@Component({
  selector: 'app-subgrantee-category-template-list',
  templateUrl: './subgrantee-category-template-list.component.html',
  styles: []
})
export class SubgranteeCategoryTemplateListComponent implements OnInit, OnDestroy {

  @Input() subgranteeTemplate: SubgranteeTemplate;

  public subgranteeCategoryTemplateArray: SubgranteeCategoryTemplate[];
  public subgranteeCategoryTemplateArrayObservable: Observable<SubgranteeCategoryTemplate[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeCategoryTemplateService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.getSubGranteeCategoryTemplates();
  }

  /**
  * Searches the awardCategoryTemplateArray
  * @param: text
  */
  search(text: string): SubgranteeCategoryTemplate[] {
    return this.subgranteeCategoryTemplateArray.filter(subgranteeCategoryTemplates => {
      const term = text.toLowerCase();
      return subgranteeCategoryTemplates.SubGranteeCatergoryTemplateDescription.toLowerCase().includes(term);
    });
  }

  getSubGranteeCategoryTemplates() {
    this.service.getSubGranteeCategoryTemplates(this.subgranteeTemplate.SubGranteeTemplateId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: SubgranteeCategoryTemplate[]) => {
        this.subgranteeCategoryTemplateArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.subgranteeCategoryTemplateArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(AwardCategoryTemplateId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteSubGranteeCategoryTemplate(AwardCategoryTemplateId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getSubGranteeCategoryTemplates();
            this.toastr.success('Deleted Successfullly', 'Award Category Template Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(sct?: SubgranteeCategoryTemplate) {
    let modalRef = this.modal.open(SubgranteeCategoryTemplateModalFormComponent);

    if (sct != null) {
      modalRef.componentInstance.modalData = Object.assign({}, sct);
    }
    modalRef.componentInstance.subgranteeTemplate = this.subgranteeTemplate;

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
       this.getSubGranteeCategoryTemplates();
      }
    });
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
