import { Component, OnInit, OnDestroy, ViewChildren, QueryList } from '@angular/core';
import { DonorService } from 'src/app/shared/services/donor.service';
import { ToastrService } from 'ngx-toastr';
import { Donor } from 'src/app/shared/models/donor.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DonorModalFormComponent } from '../donor-modal-form/donor-modal-form.component';
import { Observable, Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { SortEvent, NgbdSortableHeader } from 'src/app/shared/directives/sortable.directive';

@Component({
  selector: 'app-donor-list',
  templateUrl: './donor-list.component.html',
  styles: []
})
export class DonorListComponent implements OnInit, OnDestroy {

  public donorArrayObservable: Observable<Donor[]>;
  public total: Observable<number>;

  public ngUnsubscribe: Subject<any> = new Subject();

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;

  constructor(
    public service: DonorService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getDonors();
  }

  getDonors() {
    this.service.getDonors()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Donor[]) => {
        this.service.invokeGetDonors(response);
        this.donorArrayObservable = this.service.donors$;
        this.total = this.service.total$;
      });
  }

  onDelete(DonorId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteDonor(DonorId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getDonors();
            this.toastr.success('Deleted Successfullly', 'Donor Registration');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(d?: Donor) {
    let modalRef = this.modal.open(DonorModalFormComponent);

    if (d != null) {
      modalRef.componentInstance.modalData = Object.assign({}, d);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getDonors();
      }
    });
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  onSort({ column, direction }: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    this.service.sortColumn = column;
    this.service.sortDirection = direction;
  }

}
