import { Injectable } from '@angular/core';
import { OpportunitySubgrantee } from '../models/opportunity-subgrantee.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class OpportunitySubgranteeService {

  readonly rootURL = this.configService.config['api_root'];

  formData: OpportunitySubgrantee;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postOpportunitySubgrantee() {
    return this.http.post(this.rootURL + '/OpportunitySubGrantee', this.formData);
  }

  putOpportunitySubgrantee() {
    return this.http.put(this.rootURL + '/OpportunitySubGrantee/' + this.formData.OpportunitySubGranteeId, this.formData);
  }

  deleteOpportunitySubgrantee(id) {
    return this.http.delete(this.rootURL + '/OpportunitySubGrantee/' + id);
  }

  getOpportunitySubgrantees() {
    return this.http.get(this.rootURL + '/OpportunitySubGrantee');
  }

  getOpportunitySubgranteesFiltered(id) {
    return this.http.get(this.rootURL + '/OpportunitySubGrantee/filter/' + id);
  }

  getOpportunitySubgrantee(id) {
    return this.http.get(this.rootURL + '/OpportunitySubGrantee/' + id);
  }
}
