import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subgrantee } from 'src/app/shared/models/subgrantee.model';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { SubgranteeService } from 'src/app/shared/services/subgrantee.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { SubgranteeModalFormComponent } from '../subgrantee-modal-form/subgrantee-modal-form.component';

@Component({
  selector: 'app-subgrantee-list',
  templateUrl: './subgrantee-list.component.html',
  styles: []
})
export class SubgranteeListComponent implements OnInit, OnDestroy {

  public subGranteeArray: Subgrantee[];
  public subGranteeArrayObservable: Observable<Subgrantee[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getSubGrantees()
  }

  /**
   * Searches the donorArray
   * @param: text
   */
  search(text: string): Subgrantee[] {
    return this.subGranteeArray.filter(subGrantees => {
      const term = text.toLowerCase();
      return subGrantees.SubGranteeName.toLowerCase().includes(term) ||
        subGrantees.SubGranteeTypeDescription.toLowerCase().includes(term);
    });
  }

  getSubGrantees() {
    this.service.getSubGrantees()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Subgrantee[]) => {
        this.subGranteeArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.subGranteeArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(SubGranteeId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteSubGrantee(SubGranteeId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getSubGrantees();
            this.toastr.success('Deleted Successfullly', 'Sub Grantee Registration');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(sg?: Subgrantee) {
    let modalRef = this.modal.open(SubgranteeModalFormComponent);

    if (sg != null) {
      modalRef.componentInstance.modalData = Object.assign({}, sg);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getSubGrantees();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
