import { Component, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';
import { OfficemissionService } from 'src/app/shared/services/officemission.service';
import { AwardService } from 'src/app/shared/services/award.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Officemission } from 'src/app/shared/models/officemission.model';
import { Observable, Subject } from 'rxjs';
import { map, startWith, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-office-mission-lookup',
  templateUrl: './office-mission-lookup.component.html',
  styles: []
})
export class OfficeMissionLookupComponent implements OnInit, OnDestroy {

  public officeMissionArray: Officemission[];
  public officeMissionArrayObservable: Observable<Officemission[]>;
  public filter = new FormControl('');
  public ngUnsubscribe: Subject<any> = new Subject();

  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  constructor(
    public service: OfficemissionService,
    public awardService: AwardService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.getOfficeMissions();
  }

  /**
   * Searches the officeMissionArray
   * @param: text
   */
  search(text: string): Officemission[] {
    return this.officeMissionArray.filter(officeMissions => {
      const term = text.toLowerCase();
      return officeMissions.OfficeMissionName.toLowerCase().includes(term);
    });
  }

  getOfficeMissions() {
    this.service.getOfficeMissions()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Officemission[]) => {
        this.officeMissionArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.officeMissionArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  select(om: Officemission) {
    this.notifyParent.emit(om);
    this.activeModal.close();
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
