import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { DonorTypeService } from 'src/app/shared/services/donor-type.service';
import { ToastrService } from 'ngx-toastr';
import { DonorType } from 'src/app/shared/models/donor-type.model';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-donor-type-modal-form',
  templateUrl: './donor-type-modal-form.component.html',
  styles: []
})
export class DonorTypeModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: DonorType;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: DonorTypeService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        DonorTypeId: 0,
        DonorDescription: ''
      }
    }
  }

  onSubmit() {
    if (this.modalData.DonorTypeId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
    //this.activeModal.close();
  }

  insertRecord(model: DonorType) {
    this.service.formData = model;
    this.service.postDonorType()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Donor Type Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  updateRecord(model: DonorType) {
    this.service.formData = model;
    this.service.putDonorType()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        //this.resetForm(form);
        this.resetForm();
        this.toastr.success('Updated Successfully', 'Donor Type Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
