import { Injectable } from '@angular/core';
import { SubgranteeCategoryTemplate } from '../models/subgrantee-category-template.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class SubgranteeCategoryTemplateService {

  readonly rootURL = this.configService.config['api_root'];

  formData: SubgranteeCategoryTemplate;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postSubGranteeCategoryTemplate() {
    return this.http.post(this.rootURL + '/SubGranteeCategoryTemplate', this.formData);
  }

  putSubGranteeCategoryTemplate() {
    return this.http.put(this.rootURL + '/SubGranteeCategoryTemplate/' + this.formData.SubGranteeCategoryTemplateId, this.formData);
  }

  deleteSubGranteeCategoryTemplate(id) {
    return this.http.delete(this.rootURL + '/SubGranteeCategoryTemplate/' + id);
  }

  getSubGranteeCategoryTemplates(id) {
    return this.http.get(this.rootURL + '/SubGranteeCategoryTemplate/filter/' + id);
  }
}
