import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { OfficemissionService } from 'src/app/shared/services/officemission.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Officemission } from 'src/app/shared/models/officemission.model';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-officemission-modal-form',
  templateUrl: './officemission-modal-form.component.html',
  styles: []
})
export class OfficemissionModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: Officemission;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: OfficemissionService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        OfficeMissionId: 0,
        OfficeMissionName: ''
      }
    }
  }

  onSubmit() {
    if (this.modalData.OfficeMissionId == 0) {
      this.insertRecord(this.modalData)
    } else {
      this.updateRecord(this.modalData);
    }
    //this.activeModal.close();
  }

  insertRecord(model: Officemission) {
    this.service.formData = model;
    this.service.postOfficeMission()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Office Mission');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  updateRecord(model: Officemission) {
    this.service.formData = model;
    this.service.putOfficeMission()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm()
        this.toastr.success('Updated Successfully', 'Office Mission');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
