import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AwardDocumentation } from '../models/award-documentation.model';
import { AppConfig } from 'src/app/app-config';


@Injectable({
  providedIn: 'root'
})
export class AwardDocumentationService {
  readonly rootURL = this.configService.config['api_root'];

  public formData: AwardDocumentation;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postAwardDocumentation() {
    return this.http.post(this.rootURL + '/AwardDocumentation', this.formData);
  }

  putAwardDocumentation() {
    return this.http.put(this.rootURL + '/AwardDocumentation/' + this.formData.AwardDocumentationId, this.formData);
  }

  deleteAwardDocumentation(id) {
    return this.http.delete(this.rootURL + '/AwardDocumentation/' + id);
  }

  getAwardDocumentations(id) {
    return this.http.get(this.rootURL + '/AwardDocumentation/filter/' + id);
  }
}
