import { Component, OnInit, EventEmitter, Output, Input, OnDestroy } from '@angular/core';
import { SubgranteeCategoryTemplate } from 'src/app/shared/models/subgrantee-category-template.model';
import { SubgranteeTemplate } from 'src/app/shared/models/subgrantee-template.model';
import { Subject } from 'rxjs';
import { SubgranteeDocumentationTemplate } from 'src/app/shared/models/subgrantee-documentation-template.model';
import { SubgranteeCategoryTemplateService } from 'src/app/shared/services/subgrantee-category-template.service';
import { SubgranteeDocumentationTemplateService } from 'src/app/shared/services/subgrantee-documentation-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil } from 'rxjs/operators';
import { DocumentTypeLookupComponent } from 'src/app/lookups/document-type-lookup/document-type-lookup.component';
import { DocumentType } from 'src/app/shared/models/document-type.model';

@Component({
  selector: 'app-subgrantee-category-template-modal-form',
  templateUrl: './subgrantee-category-template-modal-form.component.html',
  styles: []
})
export class SubgranteeCategoryTemplateModalFormComponent implements OnInit, OnDestroy {
    

  @Input() modalData: SubgranteeCategoryTemplate;
  @Input() subgranteeTemplate: SubgranteeTemplate;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public subgranteeDocumentationTemplateArray: SubgranteeDocumentationTemplate[];
  public newId: number = 0;

  constructor(
    public service: SubgranteeCategoryTemplateService,
    public subgranteeDocumentationTemplateService: SubgranteeDocumentationTemplateService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
    this.getSubGranteeDocumentationTemplates();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        SubGranteeCategoryTemplateId: 0,
        SubGranteeCatergoryTemplateDescription: '',
        SubGranteeTemplateId: this.subgranteeTemplate.SubGranteeTemplateId,
        SubGranteeDocumentationTemplates: []
      }
    }
  }

  getSubGranteeDocumentationTemplates() {
    this.subgranteeDocumentationTemplateService.getSubGranteeDocumentationTemplate(this.modalData.SubGranteeCategoryTemplateId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: SubgranteeDocumentationTemplate[]) => {
        this.subgranteeDocumentationTemplateArray = response;
      });
  }

  onSubmit() {
    if (this.modalData.SubGranteeCategoryTemplateId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
  }

  insertBatchRecord(id?: number) {
    if (id != null) {
      for (let sdt of this.subgranteeDocumentationTemplateArray) {
        sdt.SubGranteeCategoryTemplateId = id;
      }
    }
    this.subgranteeDocumentationTemplateService.postBatchSubGranteeDocumentationTemplate(this.subgranteeDocumentationTemplateArray)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(response => {
        this.toastr.success('Created Successfully (Batch)', 'Sub Grantee Category Template Register');
        this.activeModal.close();
      });
  }

  deleteBatchRecord(id?: number) {
    this.subgranteeDocumentationTemplateService.deleteSubGranteeDocumentationTemplatesBatch(this.modalData.SubGranteeCategoryTemplateId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(response => {
        this.toastr.success('Deleted Successfully (Batch)', 'Sub Grantee Category Template Register');
      }, error => {
        console.log(error);
      }, () => {
        this.insertBatchRecord(id);
      })
  }

  insertRecord(model: SubgranteeCategoryTemplate) {
    this.service.formData = model;
    this.service.postSubGranteeCategoryTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        (response: SubgranteeDocumentationTemplate) => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Sub Grantee Category Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.newId = response.SubGranteeCategoryTemplateId;
        }, err => {
          console.log(err);
        }, () => {
          this.deleteBatchRecord(this.newId);
        }
      )
  }

  updateRecord(model: SubgranteeCategoryTemplate) {
    this.service.formData = model;
    this.service.putSubGranteeCategoryTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Updated Successfully', 'Sub Grantee Category Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
        }, err => {
          console.log(err);
        }, () => {
          this.deleteBatchRecord();
        }
      )
  }

  launchLookup() {
    let modalRef = this.modal.open(DocumentTypeLookupComponent);

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe((response: DocumentType) => {
      var subgranteeDocumentationTemplate = new SubgranteeDocumentationTemplate();
      subgranteeDocumentationTemplate.SubGranteeCategoryTemplateId = this.modalData.SubGranteeCategoryTemplateId;
      subgranteeDocumentationTemplate.DocumentTypeId = response.DocumentTypeId;
      subgranteeDocumentationTemplate.DocumentTypeDescription = response.DocumentTypeDescription;
      this.subgranteeDocumentationTemplateArray.push(subgranteeDocumentationTemplate);
    });
  }

  remove(id: number) {
    let index = this.subgranteeDocumentationTemplateArray.findIndex(o => o.DocumentTypeId == id);
    this.subgranteeDocumentationTemplateArray.splice(index, 1);
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
