import { Component, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';
import { DonorService } from 'src/app/shared/services/donor.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AwardService } from 'src/app/shared/services/award.service';
import { Donor } from 'src/app/shared/models/donor.model';
import { Observable, Subject } from 'rxjs';
import { map, startWith, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-donor-lookup',
  templateUrl: './donor-lookup.component.html',
  styles: []
})
export class DonorLookupComponent implements OnInit, OnDestroy {

  public donorArray: Donor[];
  public donorArrayObservable: Observable<Donor[]>;
  public filter = new FormControl('');
  public ngUnsubscribe: Subject<any> = new Subject();

  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  constructor(
    public service: DonorService,
    //public awardService: AwardService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.getDonors();
  }

  /**
   * Searches the donorArray
   * @param: text
   */
  search(text: string): Donor[] {
    return this.donorArray.filter(donors => {
      const term = text.toLowerCase();
      return donors.DonorName.toLowerCase().includes(term) ||
        donors.DonorTypeDescription.toLowerCase().includes(term);
    });
  }

  getDonors() {
    this.service.getDonors()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Donor[]) => {
        this.donorArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.donorArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  select(d: Donor) {
    this.notifyParent.emit(d);
    this.activeModal.close();
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
