import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { DonorType } from '../models/donor-type.model';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class DonorTypeService {
  readonly rootURL = this.configService.config['api_root'];

  formData: DonorType;
  list: DonorType[];

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postDonorType() {
    return this.http.post(this.rootURL + '/DonorType', this.formData);
  }

  putDonorType() {
    return this.http.put(this.rootURL + '/DonorType/' + this.formData.DonorTypeId, this.formData);
  }

  deleteDonorType(id) {
    return this.http.delete(this.rootURL + '/DonorType/' + id);
  }

  getDonorTypes() {
    return this.http.get(this.rootURL + '/DonorType');
  }
}
