import { Component, OnInit, OnDestroy } from '@angular/core';
import { AwardService } from 'src/app/shared/services/award.service';
import { ToastrService } from 'ngx-toastr';
import { Award } from 'src/app/shared/models/award.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-award-list',
  templateUrl: './award-list.component.html',
  styles: []
})
export class AwardListComponent implements OnInit, OnDestroy {

  showDocumentDetails: boolean;
  awardTitle: string;

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: AwardService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public router: Router // <-- Test
  ) { }

  ngOnInit() {
    this.getAwards();
  }

  getAwards() {
    this.service.refreshList()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Award[]) => {
        this.service.awards = response;
      });
  }

  onDelete(AwardId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteAward(AwardId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getAwards();
            this.toastr.success('Deleted Successfullly', 'Award Registration');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));

    this.showDocumentDetails = false;
    
  }

  showDataEntry(a?: Award) {
    //let modalRef = this.modal.open(AwardModalFormComponent, { size: 'lg', backdrop: 'static' });
    
    //if (a != null) {
    //  let sd = new Date(a.StartDate);
    //  a.SelectedStartDate = { year: sd.getFullYear(), month: sd.getMonth() + 1 }

    //  let ed = new Date(a.EndDate);
    //  a.SelectedEndDate = { year: ed.getFullYear(), month: ed.getMonth() + 1 }

    //  modalRef.componentInstance.modalData = Object.assign({}, a);
    //}

    //this.showDocumentDetails = false
  }

  redirect(id) {
    this.router.navigate(['/award-documentation/' + id]);
    this.service.message = 'Hello from awards module ' + id; // Test
  }

  ngOnDestroy(): void {
    this.ngUnsubscribe.next()
    this.ngUnsubscribe.complete();
  }

  

}
