import { Injectable, OnDestroy } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Injectable()
export class AppConfig implements OnDestroy {

  public config: Object = null;
  public env: Object = null;
  private ngUnsubscribe: Subject<any> = new Subject();

  constructor(public http: HttpClient) {
  }
  
  /**
   * This method:
   *   a) Loads "env.json" to get the current working environment (e.g.: 'production', 'development')
   *   b) Loads "config.[env].json" to get all env's variables (e.g.: 'env.development.json')
   */
  public load() {
    return new Promise((resolve, reject) => {
      this.http.get('./assets/env.json')
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((envResponse) => {
        this.env = envResponse["env"];
        
        let request: any = null;

        switch (this.env) {
          case 'production': {
            request = this.http.get('./assets/env.' + this.env + '.json');
          } break;

          case 'development': {
            request = this.http.get('./assets/env.' + this.env + '.json');
          } break;

          case 'default': {
            console.error('Environment file is not set or invalid');
            resolve(true);
          } break;
        }

        if (request) {
          request
          .pipe(takeUntil(this.ngUnsubscribe))
          .subscribe((responseData: any) => {
            this.config = responseData;
            resolve(true);
          });
        } else {
          console.error('Env config file "env.json" is not valid');
          resolve(true);
        }
      });

    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete()
  }
}
