import { Component, OnInit, Input, EventEmitter, Output, OnDestroy } from '@angular/core';
import { AwardCategoryTemplate } from 'src/app/shared/models/award-category-template.model';
import { AwardCategoryTemplateService } from 'src/app/shared/services/award-category-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';
import { AwardTemplate } from 'src/app/shared/models/award-template.model';
import { AwardDocumentationTemplate } from 'src/app/shared/models/award-documentation-template.model';
import { DocumentTypeLookupComponent } from 'src/app/lookups/document-type-lookup/document-type-lookup.component';
import { DocumentType } from 'src/app/shared/models/document-type.model';
import { AwardDocumentationTemplateService } from 'src/app/shared/services/award-documentation-template.service';

@Component({
  selector: 'app-award-category-template-modal-form',
  templateUrl: './award-category-template-modal-form.component.html',
  styles: []
})
export class AwardCategoryTemplateModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: AwardCategoryTemplate;
  @Input() awardTemplate: AwardTemplate;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public awardDocumentationTemplateArray: AwardDocumentationTemplate[];
  public newId: number = 0;

  constructor(
    public service: AwardCategoryTemplateService,
    public awardDocumentationTemplateService: AwardDocumentationTemplateService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
    this.getAwardDocumentationTemplates();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        AwardCategoryTemplateId: 0,
        AwardCategoryTemplateDescription: '',
        AwardTemplateId: this.awardTemplate.AwardTemplateId,
        AwardDocumentationTemplates: []
      }
    }
  }

  getAwardDocumentationTemplates() {
    this.awardDocumentationTemplateService.getAwardDocumentationTemplates(this.modalData.AwardCategoryTemplateId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: AwardDocumentationTemplate[]) => {
        this.awardDocumentationTemplateArray = response;
      });
  }

  onSubmit() {
    if (this.modalData.AwardCategoryTemplateId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
  }

  insertBatchRecord(id?: number) {
    if (id != null) {
      for (let ad of this.awardDocumentationTemplateArray) {
        ad.AwardCategoryTemplateId = id;
      }
    }
    this.awardDocumentationTemplateService.postBatchAwardDocumentationTemplate(this.awardDocumentationTemplateArray)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(response => {
        this.toastr.success('Created Successfully (Batch)', 'Award Category Template Register');
        this.activeModal.close();
      });
  }

  deleteBatchRecord(id?: number) {
    this.awardDocumentationTemplateService.deleteAwardDocumentationTemplatesBatch(this.modalData.AwardCategoryTemplateId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(response => {
        this.toastr.success('Deleted Successfully (Batch)', 'Award Category Template Register');
      }, error => {
        console.log(error);
      }, () => {
        this.insertBatchRecord(id);
      })
  }

  insertRecord(model: AwardCategoryTemplate) {
    this.service.formData = model;
    this.service.postAwardCategoryTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        (response: AwardDocumentationTemplate) => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Award Category Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.newId = response.AwardCategoryTemplateId;
        }, err => {
          console.log(err);
        }, () => {
          this.deleteBatchRecord(this.newId);
        }
      )
  }

  updateRecord(model: AwardCategoryTemplate) {
    this.service.formData = model;
    this.service.putAwardCategoryTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Updated Successfully', 'Award Category Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
        }, err => {
          console.log(err);
        }, () => {
          this.deleteBatchRecord();
        }
      )
  }

  launchLookup() {
      let modalRef = this.modal.open(DocumentTypeLookupComponent);

      // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe((response: DocumentType) => {
        var awardDocumentationTemplate = new AwardDocumentationTemplate();
        awardDocumentationTemplate.AwardCategoryTemplateId = this.modalData.AwardCategoryTemplateId;
        awardDocumentationTemplate.DocumentTypeId = response.DocumentTypeId;
        awardDocumentationTemplate.DocumentTypeDescription = response.DocumentTypeDescription;
        this.awardDocumentationTemplateArray.push(awardDocumentationTemplate);
      });
  }

  remove(id: number) {
    let index = this.awardDocumentationTemplateArray.findIndex(o => o.DocumentTypeId == id);
    this.awardDocumentationTemplateArray.splice(index, 1);
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
