import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { SubgranteeType } from 'src/app/shared/models/subgrantee-type.model';
import { SubgranteeTypeService } from 'src/app/shared/services/subgrantee-type.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-subgrantee-type-modal-form',
  templateUrl: './subgrantee-type-modal-form.component.html',
  styles: []
})
export class SubgranteeTypeModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: SubgranteeType;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeTypeService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        SubGranteeTypeId: 0,
        SubGranteeTypeDescription: ''
      }
    }
  }

  onSubmit() {
    if (this.modalData.SubGranteeTypeId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
  }

  insertRecord(model: SubgranteeType) {
    this.service.formData = model;
    this.service.postSubGranteeType()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Sub Grantee Type Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(model: SubgranteeType) {
    this.service.formData = model;
    this.service.putSubGranteeType()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          //this.resetForm(form);
          this.resetForm();
          this.toastr.success('Updated Successfully', 'Sub Grantee Type Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
