import { Component, OnInit, OnDestroy } from '@angular/core';
import { DonorTypeService } from 'src/app/shared/services/donor-type.service';
import { DonorType } from 'src/app/shared/models/donor-type.model';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DonorTypeModalFormComponent } from '../donor-type-modal-form/donor-type-modal-form.component';
import { Observable, Subject } from 'rxjs';
import { map, startWith, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-donor-type-list',
  templateUrl: './donor-type-list.component.html',
  styles: []
})
export class DonorTypeListComponent implements OnInit, OnDestroy {

  public donorTypeArray: DonorType[];
  public donorTypeArrayObservable: Observable<DonorType[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: DonorTypeService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getDonorTypes();
  }

  /**
   * Searches the donorTypeArray
   * @param: text
   */
  search(text: string): DonorType[] {
    return this.donorTypeArray.filter(donorTypes => {
      const term = text.toLowerCase();
      return donorTypes.DonorDescription.toLowerCase().includes(term);
    });
  }

  getDonorTypes() {
    this.service.getDonorTypes()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: DonorType[]) => {
        this.donorTypeArray = response;
      }, err => {
        console.log(err);
      },
      () => {
        this.donorTypeArrayObservable = this.filter.valueChanges.pipe(
          startWith(''),
          map(text => this.search(text))
        );
      });
  }

  onDelete(DonorTypeId, deleteModal) {
    
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteDonorType(DonorTypeId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getDonorTypes();
            this.toastr.success('Deleted Successfullly', 'Donor Type Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(dt?: DonorType) {
    let modalRef = this.modal.open(DonorTypeModalFormComponent);

    if (dt != null) {
      modalRef.componentInstance.modalData = Object.assign({}, dt);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getDonorTypes();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
