import { Injectable } from '@angular/core';
import { Subgrantee } from '../models/subgrantee.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class SubgranteeService {
  readonly rootURL = this.configService.config['api_root'];

  formData: Subgrantee;
  list: Subgrantee[];

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postSubGrantee() {
    return this.http.post(this.rootURL + '/SubGrantee', this.formData);
  }

  putSubGrantee() {
    return this.http.put(this.rootURL + '/SubGrantee/' + this.formData.SubGranteeId, this.formData);
  }

  deleteSubGrantee(id) {
    return this.http.delete(this.rootURL + '/SubGrantee/' + id);
  }

  getSubGrantees() {
    return this.http.get(this.rootURL + '/SubGrantee');
  }
}
