import { Component, OnInit, Input, OnDestroy } from '@angular/core';
import { AwardCategoryTemplate } from 'src/app/shared/models/award-category-template.model';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { AwardCategoryTemplateService } from 'src/app/shared/services/award-category-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AwardTemplate } from 'src/app/shared/models/award-template.model';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { AwardCategoryTemplateModalFormComponent } from '../award-category-template-modal-form/award-category-template-modal-form.component';

@Component({
  selector: 'app-award-category-template-list',
  templateUrl: './award-category-template-list.component.html',
  styles: []
})
export class AwardCategoryTemplateListComponent implements OnInit, OnDestroy {

  @Input() awardTemplate: AwardTemplate;

  public awardCategoryTemplateArray: AwardCategoryTemplate[];
  public awardCategoryTemplateArrayObservable: Observable<AwardCategoryTemplate[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: AwardCategoryTemplateService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.getAwardCategoryTemplates();
  }

  /**
   * Searches the awardCategoryTemplateArray
   * @param: text
   */
  search(text: string): AwardCategoryTemplate[] {
    return this.awardCategoryTemplateArray.filter(awardCategoryTemplates => {
      const term = text.toLowerCase();
      return awardCategoryTemplates.AwardCategoryTemplateDescription.toLowerCase().includes(term);
    });
  }

  getAwardCategoryTemplates() {
    this.service.getAwardCategoryTemplates(this.awardTemplate.AwardTemplateId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: AwardCategoryTemplate[]) => {
        this.awardCategoryTemplateArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.awardCategoryTemplateArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(AwardCategoryTemplateId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteAwardCategoryTemplate(AwardCategoryTemplateId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getAwardCategoryTemplates();
            this.toastr.success('Deleted Successfullly', 'Award Category Template Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(awt?: AwardCategoryTemplate) {
    let modalRef = this.modal.open(AwardCategoryTemplateModalFormComponent);

    if (awt != null) {
      modalRef.componentInstance.modalData = Object.assign({}, awt);
    }
    modalRef.componentInstance.awardTemplate = this.awardTemplate;

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this,this.getAwardCategoryTemplates();
      }
    });
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }


}
