import { Component, OnInit, OnDestroy } from '@angular/core';
import { DocumentType } from 'src/app/shared/models/document-type.model';
import { FormControl } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { map, startWith, takeUntil } from 'rxjs/operators';
import { DocumentTypeService } from 'src/app/shared/services/document-type.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { DocumentTypeModalFormComponent } from '../document-type-modal-form/document-type-modal-form.component';

@Component({
  selector: 'app-document-type-list',
  templateUrl: './document-type-list.component.html',
  styles: []
})
export class DocumentTypeListComponent implements OnInit, OnDestroy {

  public documentTypeArray: DocumentType[];
  public documentTypeArrayObservable: Observable<DocumentType[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: DocumentTypeService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getDocumentTypes();
  }

  /**
   * Searches the documentTypeArray
   * @param: text
   */
  search(text: string): DocumentType[] {
    return this.documentTypeArray.filter(documentTypes => {
      const term = text.toLowerCase();
      return documentTypes.DocumentTypeDescription.toLowerCase().includes(term);
    });
  }

  getDocumentTypes() {
    this.service.getDocumentTypes()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: DocumentType[]) => {
        this.documentTypeArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.documentTypeArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(DocumentTypeId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteDocumentType(DocumentTypeId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getDocumentTypes();
            this.toastr.success('Deleted Successfullly', 'Document Type Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(dt?: DocumentType) {
    let modalRef = this.modal.open(DocumentTypeModalFormComponent);

    if (dt != null) {
      modalRef.componentInstance.modalData = Object.assign({}, dt);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getDocumentTypes();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
