import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class AwardDocumentationAttachmentService {

  readonly rootURL = this.configService.config['api_root'];

  constructor(
    public http: HttpClient,
    public configService: AppConfig
  ) { }

  upload(files, awardDocumentationId) {
    if (files.length === 0)
      return;

    const formData = new FormData();

    for (let file of files)
      formData.append(file.name, file);

    const uploadReq = new HttpRequest('POST', this.rootURL + '/AwardDocumentationAttachment/UploadFile/' + awardDocumentationId, formData, {
      reportProgress: true,
    });

    return this.http.request(uploadReq);
  }

  deleteAttachment(id) {
    return this.http.delete(this.rootURL + '/AwardDocumentationAttachment/' + id);
  }

  download(id) {
    window.location.href = this.rootURL + '/AwardDocumentationAttachment/download/' + id;
  }

  getAwardDocumentationAttachments(awardDocumentationId) {
    return this.http.get(this.rootURL + '/AwardDocumentationAttachment/filter/' + awardDocumentationId);
  }
}
