import { Injectable } from '@angular/core';
import { SubgranteeDocumentation } from '../models/subgrantee-documentation.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class SubgranteeDocumentationService {
  readonly rootURL = this.configService.config['api_root'];

  public formData: SubgranteeDocumentation;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postSubGranteeDocumentation() {
    return this.http.post(this.rootURL + '/SubGranteeDocumentation', this.formData);
  }

  putSubGranteeDocumentation() {
    return this.http.put(this.rootURL + '/SubGranteeDocumentation/' + this.formData.SubGranteeDocumentationId, this.formData);
  }

  deleteSubGranteeDocumentation(id) {
    return this.http.delete(this.rootURL + '/SubGranteeDocumentation/' + id);
  }

  getSubGranteeDocumentations(id) {
    return this.http.get(this.rootURL + '/SubGranteeDocumentation/filter/' + id);
  }
}
