import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { DonorService } from 'src/app/shared/services/donor.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Donor } from 'src/app/shared/models/donor.model';
import { DonorTypeService } from 'src/app/shared/services/donor-type.service';
import { DonorType } from 'src/app/shared/models/donor-type.model';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-donor-modal-form',
  templateUrl: './donor-modal-form.component.html',
  styles: []
})
export class DonorModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: Donor;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public donorTypes: DonorType[];

  constructor(
    public service: DonorService,
    public donorTypeService: DonorTypeService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
    this.getDonorTypes();
  }

  getDonorTypes() {
    this.donorTypeService.getDonorTypes()
      .subscribe((response: DonorType[]) => {
        this.donorTypes = response;
      });
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        DonorId: 0,
        DonorTypeId: 0,
        DonorName: '',
        DonorTypeDescription: ''
      }
    }
  }

  onSubmit() {
    if (this.modalData.DonorId == 0) {
      this.insertRecord(this.modalData)
    } else {
      this.updateRecord(this.modalData);
    }
    //this.activeModal.close();
  }

  insertRecord(model: Donor) {
    this.service.formData = model;
    this.service.postDonor()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Donor');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  updateRecord(model: Donor) {
    this.service.formData = model;
    this.service.putDonor()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm()
        this.toastr.success('Updated Successfully', 'Donor');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete()
  }

}
