import { Injectable } from '@angular/core';
import { AppConfig } from 'src/app/app-config';
import { SubgranteeTemplate } from '../models/subgrantee-template.model';
import { HttpClient } from '@angular/common/http';
import { SubgranteeTemplateIds } from '../models/subgrantee-template-ids.model';


@Injectable({
  providedIn: 'root'
})
export class SubgranteeTemplateService {

  readonly rootURL = this.configService.config['api_root'];

  formData: SubgranteeTemplate;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postSubGranteeTemplate() {
    return this.http.post(this.rootURL + '/SubGranteeTemplate', this.formData);
  }

  postSubGranteeTemplateBatch(subgranteeTemplateIds: SubgranteeTemplateIds) {
    return this.http.post(this.rootURL + '/SubGranteeTemplate/Batch', subgranteeTemplateIds);
  }

  putSubGranteeTemplate() {
    return this.http.put(this.rootURL + '/SubGranteeTemplate/' + this.formData.SubGranteeTemplateId, this.formData);
  }

  deleteSubGranteeTemplate(id) {
    return this.http.delete(this.rootURL + '/SubGranteeTemplate/' + id);
  }

  getSubGranteeTemplates() {
    return this.http.get(this.rootURL + '/SubGranteeTemplate');
  }

  getSubGranteeTemplatesFiltered() {
    return this.http.get(this.rootURL + '/SubGranteeTemplate/filter');
  }

  getSubGranteeTemplateById(id) {
    return this.http.get(this.rootURL + '/SubGranteeTemplate/filter/' + id);
  }
}
