import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Donor } from '../models/donor.model';
import { SortDirection } from '../directives/sortable.directive';
import { BehaviorSubject, Subject, Observable, of } from 'rxjs';
import { tap, debounceTime, switchMap, delay } from 'rxjs/operators';
import { AppConfig } from 'src/app/app-config';

interface SearchResult {
  donors: Donor[];
  total: number;
}

interface State {
  page: number;
  pageSize: number;
  searchTerm: string;
  sortColumn: string;
  sortDirection: SortDirection;
}

function compare(v1, v2) {
  return v1 < v2 ? -1 : v1 > v2 ? 1 : 0;
}

function sort(donors: Donor[], column: string, direction: string): Donor[] {
  if (direction === '') {
    return donors;
  } else {
    return [...donors].sort((a, b) => {
      const res = compare(a[column], b[column]);
      return direction === 'asc' ? res : -res;
    });
  }
}

function matches(donor: Donor, term: string) {
  return donor.DonorName.toLowerCase().includes(term) ||
    donor.DonorTypeDescription.toLowerCase().includes(term);
}


@Injectable({
  providedIn: 'root'
})
export class DonorService {

  private _loading$ = new BehaviorSubject<boolean>(true);
  private _search$ = new Subject<void>();
  private _donors$ = new BehaviorSubject<Donor[]>([]);
  private _total$ = new BehaviorSubject<number>(0);

  private _state: State = {
    page: 1,
    pageSize: 4,
    searchTerm: '',
    sortColumn: '',
    sortDirection: ''
  };

  donorArray: Donor[];

  readonly rootURL = this.configService.config['api_root'];

  formData: Donor;
  list: Donor[];

  constructor(public http: HttpClient, public configService: AppConfig) { }

  invokeGetDonors(donors: Donor[]) {

    this.donorArray = donors;

    this._search$.pipe(
      tap(() => this._loading$.next(true)),
      debounceTime(100),
      switchMap(() => this._search()),
      delay(100),
      tap(() => this._loading$.next(false))
    ).subscribe(result => {
      this._donors$.next(result.donors);
      this._total$.next(result.total);
    });

    this._search$.next();
  }

  get donors$() { return this._donors$.asObservable(); }
  get total$() { return this._total$.asObservable(); }
  get loading$() { return this._loading$.asObservable(); }
  get page() { return this._state.page; }
  get pageSize() { return this._state.pageSize; }
  get searchTerm() { return this._state.searchTerm; }

  set page(page: number) { this._set({ page }); }
  set pageSize(pageSize: number) { this._set({ pageSize }); }
  set searchTerm(searchTerm: string) { this._set({ searchTerm }); }
  set sortColumn(sortColumn: string) { this._set({ sortColumn }); }
  set sortDirection(sortDirection: SortDirection) { this._set({ sortDirection }); }

  private _set(patch: Partial<State>) {
    Object.assign(this._state, patch);
    this._search$.next();
  }

  private _search(): Observable<SearchResult> {
    const { sortColumn, sortDirection, pageSize, page, searchTerm } = this._state;

    // 1. sort
    let donors = sort(this.donorArray, sortColumn, sortDirection);

    // 2. filter
    donors = donors.filter(donor => matches(donor, searchTerm));
    const total = donors.length;

    // 3. paginate
    donors = donors.slice((page - 1) * pageSize, (page - 1) * pageSize + pageSize);
    return of({ donors, total });
  }

  postDonor() {
    return this.http.post(this.rootURL + '/Donor', this.formData);
  }

  putDonor() {
    return this.http.put(this.rootURL + '/Donor/' + this.formData.DonorId, this.formData);
  }

  deleteDonor(id) {
    return this.http.delete(this.rootURL + '/Donor/' + id);
  }

  getDonors() {
    return this.http.get(this.rootURL + '/Donor');
  }
}
