import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { SubgranteeCategory } from '../models/subgrantee-category.model';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class SubgranteeCategoryService {

  readonly rootURL = this.configService.config['api_root'];

  formData: SubgranteeCategory;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postSubGranteeCategory() {
    return this.http.post(this.rootURL + '/SubGranteeCategory', this.formData);
  }

  postSubGranteeCategoryBatch(data: SubgranteeCategory) {
    return this.http.post(this.rootURL + '/SubGranteeCategory/Batch', data);
  }

  putSubGranteeCategory() {
    return this.http.put(this.rootURL + '/SubGranteeCategory/' + this.formData.SubGranteeCategoryId, this.formData);
  }

  deleteSubGranteeCategory(id) {
    return this.http.delete(this.rootURL + '/SubGranteeCategory/' + id);
  }

  getSubGranteeCategories(id) {
    return this.http.get(this.rootURL + '/SubGranteeCategory/filter/' + id);
  }
}
