import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Subject } from 'rxjs';
import { SubgranteeCategory } from 'src/app/shared/models/subgrantee-category.model';
import { SubgranteeCategoryService } from 'src/app/shared/services/subgrantee-category.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil } from 'rxjs/operators';
import { SubgranteeTemplate } from 'src/app/shared/models/subgrantee-template.model';
import { SubgranteeTemplateService } from 'src/app/shared/services/subgrantee-template.service';
import { SubgranteeTemplateIds } from 'src/app/shared/models/subgrantee-template-ids.model';

@Component({
  selector: 'app-subgrantee-category-modal-form',
  templateUrl: './subgrantee-category-modal-form.component.html',
  styles: []
})
export class SubgranteeCategoryModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: SubgranteeCategory
  @Input() create: boolean
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();
  public subgranteeTemplates: SubgranteeTemplate[];

  //// Used in toggling controls
  public createOneDisabled: boolean = false;
  public createDefaultTemplateDisabled: boolean = false;
  public createSelectTemplateDisabled: boolean = false;

  //// Used in toggling save mode
  public createOne: boolean = false;
  public createDefaultTemplate = false;
  public createSelectTemplate = false;

  //// TODO: change this to actual modalData
  public selectedTemplate: any;

  constructor(
    public service: SubgranteeCategoryService,
    public subgranteeTemplateService: SubgranteeTemplateService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
    this.getSubGranteeTemplates();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        SubGranteeCategoryId: 0,
        OpportunitySubGranteeId: 0,
        SubGranteeTemplateId: 0,
        SubGranteeCategoryDescription: '',
        SubGranteeDocumentations: []
      }
    }
  }

  getSubGranteeTemplates() {
    this.subgranteeTemplateService.getSubGranteeTemplatesFiltered()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: SubgranteeTemplate[]) => {
        this.subgranteeTemplates = response;
      });
  }

  onSubmit() {
    if (!this.create || this.createOne) {
      if (this.modalData.SubGranteeCategoryId == 0) {
        this.insertRecord(this.modalData);
      } else {
        this.updateRecord(this.modalData);
      }
    }
    if (this.createDefaultTemplate) {
      var ids = new SubgranteeTemplateIds();
      ids.OpportunitySubGranteeId = this.modalData.OpportunitySubGranteeId;
      ids.SubGranteeTemplateId = 0;
      this.insertRecordBatch(ids);
    }
    if (this.createSelectTemplate) {
      var ids = new SubgranteeTemplateIds();
      ids.OpportunitySubGranteeId = this.modalData.OpportunitySubGranteeId;
      ids.SubGranteeTemplateId = this.modalData.SubGranteeTemplateId;
      this.insertRecordBatch(ids);
    }
  }

  insertRecordBatch(subgranteeTemplateIds: SubgranteeTemplateIds) {
    this.subgranteeTemplateService.postSubGranteeTemplateBatch(subgranteeTemplateIds)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response) => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Sub Grantee Category Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      })
  }

  insertRecord(model: SubgranteeCategory) {
    this.service.formData = model;
    this.service.postSubGranteeCategory()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Sub Grantee Category Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(model: SubgranteeCategory) {
    this.service.formData = model;
    this.service.putSubGranteeCategory()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Updated Successfully', 'Sub Grantee Category Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  closeModal() {
    this.activeModal.close();
  }

  onChangeDefaultTemplate(e) {
    if (e.target.checked) {
      // disables the input and dropdown control
      this.createOneDisabled = true;
      this.createSelectTemplateDisabled = true;

      // tags the create from default when saving
      this.createOne = false;
      this.createDefaultTemplate = true;
      this.createSelectTemplate = false;
    } else {
      // enables the input and dropdown control
      this.createOneDisabled = false;
      this.createSelectTemplateDisabled = false;
    }
  }

  onChangeCreateOne() {
    if (this.create) {
      // Execute this on create mode only
      if (this.modalData.SubGranteeCategoryDescription != '') {
        // disables the dropdown and checkbox control
        this.createSelectTemplateDisabled = true;
        this.createDefaultTemplateDisabled = true;

        // tags the create one when saving
        this.createOne = true;
        this.createDefaultTemplate = false;
        this.createSelectTemplate = false;
      } else {
        // enables dropdown and checkbox control
        this.createSelectTemplateDisabled = false;
        this.createDefaultTemplateDisabled = false;
      }
    }
  }

  onChangeSelectTemplate() {
    if (this.selectedTemplate != 0) {
      // disables the input and checkbox control
      this.createOneDisabled = true;
      this.createDefaultTemplateDisabled = true;

      // tags the create from selected template when saving
      this.createOne = false;
      this.createDefaultTemplate = false;
      this.createSelectTemplate = true;
    } else {
      // enables the input and checkbox control
      this.createOneDisabled = false;
      this.createDefaultTemplateDisabled = false;
    }
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
