import { Component, OnInit, OnDestroy } from '@angular/core';
import { Opportunity } from 'src/app/shared/models/opportunity.model';
import { Subject } from 'rxjs';
import { Award } from 'src/app/shared/models/award.model';
import { OpportunitySubgrantee } from 'src/app/shared/models/opportunity-subgrantee.model';
import { ActivatedRoute, Router } from '@angular/router';
import { AwardService } from 'src/app/shared/services/award.service';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { OpportunitySubgranteeService } from 'src/app/shared/services/opportunity-subgrantee.service';
import { OpportunityService } from 'src/app/shared/services/opportunity.service';
import { takeUntil } from 'rxjs/operators';
import { SubgranteeLookupComponent } from 'src/app/lookups/subgrantee-lookup/subgrantee-lookup.component';
import { Subgrantee } from 'src/app/shared/models/subgrantee.model';

@Component({
  selector: 'app-opportunity-subgrantee-data-entry',
  templateUrl: './opportunity-subgrantee-data-entry.component.html',
  styles: []
})
export class OpportunitySubgranteeDataEntryComponent implements OnInit, OnDestroy {

  public opportunitySubGranteeId: number = 0;
  public opportunityId: number = 0;
  public opportunity: Opportunity;
  public opportunitySubGrantee: OpportunitySubgrantee

  public ngUnsubscribe: Subject<any> = new Subject();

  public showSubgranteeDocumentation: boolean = false

  constructor(
    public route: ActivatedRoute,
    public router: Router,
    public service: OpportunitySubgranteeService,
    public opportunityService: OpportunityService,
    public awardService: AwardService,
    public modal: NgbModal,
    public toastr: ToastrService
  ) {
    this.getRouteParam()
  }

  ngOnInit() {
    this.initializeVariables();
  }

  getRouteParam() {
    this.route.paramMap
      .subscribe(params => {
        if (+params.get('id') != 0) {
          this.opportunitySubGranteeId = +params.get('id');
          this.getOpportunitySubgrantee();
          this.showSubgranteeDocumentation = true;
        }
        if (+params.get('opportunityId') != 0) {
          this.opportunityId = +params.get('opportunityId');
          this.getOpportunity();
        }
      });
  }

  getOpportunitySubgrantee() {
    this.service.getOpportunitySubgrantee(this.opportunitySubGranteeId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: OpportunitySubgrantee) => {
        this.opportunitySubGrantee = response;
      });
  }

  getOpportunity() {
    this.opportunityService.getOpportunity(this.opportunityId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Opportunity) => {
        this.opportunity = response;
      });
  }

  initializeVariables() {
    this.opportunity = {
      RFPId: 0,
      RFPName: '',
      AwardId: 0,
      DeadlineDate: null,
      DecisionDate: null,
      AwardNumber: ''
    }
    this.opportunitySubGrantee = {
      OpportunitySubGranteeId: 0,
      RFPId: 0,
      SubGranteeId: 0,
      SubGranteeName: '',
      SubGranteeTypeDescription: '',
      AwardNumber: '',
      RFPName: ''
    }
  }

  redirect(id, opportunityId) {
    this.router.navigate(['/opportunity-subgrantee-data-entry/' + id + '/' + opportunityId]);
  }

  launchLookup() {
    let modalRef = this.modal.open(SubgranteeLookupComponent);

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe((response: Subgrantee) => {
      this.opportunitySubGrantee.SubGranteeId = response.SubGranteeId;
      this.opportunitySubGrantee.SubGranteeName = response.SubGranteeName;
      this.opportunitySubGrantee.SubGranteeTypeDescription = response.SubGranteeTypeDescription;
    });
  }

  onSave() {
    if (this.opportunitySubGrantee.OpportunitySubGranteeId == 0) {
      this.insertRecord(this.opportunitySubGrantee)
    } else {
      this.updateRecord(this.opportunitySubGrantee);
    }
  }

  insertRecord(a: OpportunitySubgrantee) {
    this.opportunitySubGrantee.RFPId = this.opportunity.RFPId;
    this.service.formData = a;
    this.service.postOpportunitySubgrantee()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        (response: OpportunitySubgrantee) => {
          this.toastr.success('Submitted Successfully', 'Opportunity Sub Grantee Register');
          this.redirect(response.OpportunitySubGranteeId, this.opportunity.RFPId);
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(a: OpportunitySubgrantee) {
    this.service.formData = a;
    this.service.putOpportunitySubgrantee()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        response => {
          this.toastr.info('Updated Successfully', 'Opportunity Sub Grantee Register');
        },
        err => {
          console.log(err);
        }
      )
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
