import { Injectable } from '@angular/core';
import { AwardCategoryTemplate } from '../models/award-category-template.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class AwardCategoryTemplateService {

  readonly rootURL = this.configService.config['api_root'];

  formData: AwardCategoryTemplate;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postAwardCategoryTemplate() {
    return this.http.post(this.rootURL + '/AwardCategoryTemplate', this.formData);
  }

  putAwardCategoryTemplate() {
    return this.http.put(this.rootURL + '/AwardCategoryTemplate/' + this.formData.AwardCategoryTemplateId, this.formData);
  }

  deleteAwardCategoryTemplate(id) {
    return this.http.delete(this.rootURL + '/AwardCategoryTemplate/' + id);
  }

  getAwardCategoryTemplates(id) {
    return this.http.get(this.rootURL + '/AwardCategoryTemplate/filter/' + id);
  }
}
