import { Injectable } from '@angular/core';
import { SubgranteeDocumentationTemplate } from '../models/subgrantee-documentation-template.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class SubgranteeDocumentationTemplateService {

  readonly rootURL = this.configService.config['api_root'];

  formData: SubgranteeDocumentationTemplate;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postSubGranteeDocumentationTemplate() {
    return this.http.post(this.rootURL + '/SubGranteeDocumentationTemplate', this.formData);
  }

  postBatchSubGranteeDocumentationTemplate(subgranteeDocumentationTemplates: SubgranteeDocumentationTemplate[]) {
    return this.http.post(this.rootURL + '/SubGranteeDocumentationTemplate/Batch', subgranteeDocumentationTemplates);
  }

  putSubGranteeDocumentationTemplate() {
    return this.http.put(this.rootURL + '/SubGranteeDocumentationTemplate/' + this.formData.SubGranteeCategoryTemplateId, this.formData);
  }

  deleteSubGranteeDocumentationTemplate(id) {
    return this.http.delete(this.rootURL + '/SubGranteeDocumentationTemplate/' + id);
  }

  deleteSubGranteeDocumentationTemplatesBatch(id) {
    return this.http.delete(this.rootURL + '/SubGranteeDocumentationTemplate/Batch/' + id);
  }

  getSubGranteeDocumentationTemplate(id) {
    return this.http.get(this.rootURL + '/SubGranteeDocumentationTemplate/filter/' + id);
  }
}
