import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AwardDataEntryComponent } from './award/award-data-entry/award-data-entry.component';
import { AwardTemplateListComponent } from './award-template/award-template-list/award-template-list.component';
import { DonorTypeListComponent } from './donor-type/donor-type-list/donor-type-list.component';
import { OfficemissionListComponent } from './officemission/officemission-list/officemission-list.component';
import { DocumentTypeListComponent } from './document-type/document-type-list/document-type-list.component';
import { StatusListComponent } from './status/status-list/status-list.component';
import { DonorListComponent } from './donor/donor-list/donor-list.component';
import { OpportunityListComponent } from './opportunity/opportunity-list/opportunity-list.component';
import { AwardListComponent } from './award/award-list/award-list.component';
import { SubgranteeListComponent } from './subgrantee/subgrantee-list/subgrantee-list.component';
import { SubgranteeTypeListComponent } from './subgrantee-type/subgrantee-type-list/subgrantee-type-list.component';
import { OpportunitySubgranteeListComponent } from './opportunity-subgrantee/opportunity-subgrantee-list/opportunity-subgrantee-list.component';
import { OpportunitySubgranteeDataEntryComponent } from './opportunity-subgrantee/opportunity-subgrantee-data-entry/opportunity-subgrantee-data-entry.component';
import { SubgranteeTemplateListComponent } from './subgrantee-template/subgrantee-template-list/subgrantee-template-list.component';

const routes: Routes = [
  { path: 'donor', component: DonorListComponent },
  { path: 'donortypes', component: DonorTypeListComponent },
  { path: 'officemission', component: OfficemissionListComponent },
  { path: 'award', component: AwardListComponent },
  { path: 'opportunity', component: OpportunityListComponent },
  { path: 'award-data-entry/:id', component: AwardDataEntryComponent },
  { path: 'document-type', component: DocumentTypeListComponent },
  { path: 'status', component: StatusListComponent },
  { path: 'award-template', component: AwardTemplateListComponent },
  { path: 'subgrantee', component: SubgranteeListComponent },
  { path: 'subgrantee-type', component: SubgranteeTypeListComponent },
  { path: 'opportunity-subgrantee/:id', component: OpportunitySubgranteeListComponent },
  { path: 'opportunity-subgrantee-data-entry/:id/:opportunityId', component: OpportunitySubgranteeDataEntryComponent },
  { path: 'subgrantee-template', component: SubgranteeTemplateListComponent },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [
  DonorListComponent,
  DonorTypeListComponent,
  OfficemissionListComponent,
  DocumentTypeListComponent,
  AwardListComponent,
  OpportunityListComponent,
  StatusListComponent,
  AwardDataEntryComponent,
  AwardTemplateListComponent,
  SubgranteeListComponent,
  SubgranteeTypeListComponent,
  OpportunitySubgranteeListComponent,
  OpportunitySubgranteeDataEntryComponent,
  SubgranteeTemplateListComponent
]
