import { Injectable } from '@angular/core';
import { AwardCategory } from '../models/award-category.model';
import { HttpClient } from '@angular/common/http';
import { AwardTemplate } from '../models/award-template.model';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class AwardCategoryService {

  readonly rootURL = this.configService.config['api_root'];

  formData: AwardCategory;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postAwardCategory() {
    return this.http.post(this.rootURL + '/AwardCategory', this.formData);
  }

  postAwardCategoryBatch(data: AwardTemplate) {
    return this.http.post(this.rootURL + '/AwardCategory/Batch', data);
  }

  putAwardCategory() {
    return this.http.put(this.rootURL + '/AwardCategory/' + this.formData.AwardCategoryId, this.formData);
  }

  deleteAwardCategory(id) {
    return this.http.delete(this.rootURL + '/AwardCategory/' + id);
  }

  getAwardCategories(id) {
    return this.http.get(this.rootURL + '/AwardCategory/filter/' + id);
  }
}
