import { Component, OnInit, Input, OnDestroy, Output, EventEmitter } from '@angular/core';
import { AwardDocumentationAttachmentService } from 'src/app/shared/services/award-documentation-attachment.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal, NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { AwardDocumentationAttachmentModalFormComponent } from '../award-documentation-attachment-modal-form/award-documentation-attachment-modal-form.component';
import { AwardDocumentationAttachment } from 'src/app/shared/models/award-documentation-attachment.model';
import { Observable, Subject } from 'rxjs';
import { map, startWith, takeUntil } from 'rxjs/operators';
import { AwardDocumentation } from 'src/app/shared/models/award-documentation.model';
import { FormControl } from '@angular/forms';

@Component({
  selector: 'app-award-documentation-attachment-list',
  templateUrl: './award-documentation-attachment-list.component.html',
  styles: []
})
export class AwardDocumentationAttachmentListComponent implements OnInit, OnDestroy {

  @Input() awardDocumentation: AwardDocumentation;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public awardDocumentationAttachmentArray: AwardDocumentationAttachment[];
  public awardDocumentationAttachmentArrayObservable: Observable<AwardDocumentationAttachment[]>;
  public filter = new FormControl('');

  constructor(
    public service: AwardDocumentationAttachmentService,
    public toastr: ToastrService,
    public modal: NgbModal,
    public activeModal: NgbActiveModal
  ) { }

  /**
   * Searches the donorArray
   * @param: text
   */
  search(text: string): AwardDocumentationAttachment[] {
    return this.awardDocumentationAttachmentArray.filter(att => {
      const term = text.toLowerCase();
      return att.FileName.toLowerCase().includes(term);
    });
  }

  ngOnInit() {
    this.getAwardDocumentationAttachments();
  }

  getAwardDocumentationAttachments() {
    this.service.getAwardDocumentationAttachments(this.awardDocumentation.AwardDocumentationId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: AwardDocumentationAttachment[]) => {
        this.awardDocumentationAttachmentArray = response;
      }, err => {
        console.log(err);
      },
      () => {
        this.awardDocumentationAttachmentArrayObservable = this.filter.valueChanges.pipe(
          startWith(''),
          map(text => this.search(text))
        );
      });
  }

  onDelete(AwardDocumentationAttachmentId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteAttachment(AwardDocumentationAttachmentId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
        res => {
          this.getAwardDocumentationAttachments();
            this.toastr.success('Deleted Successfullly', 'Award Registration');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  download(id) {
    this.service.download(id);
  }

  showFileUpload() {
    let modalRef = this.modal.open(AwardDocumentationAttachmentModalFormComponent);
    modalRef.componentInstance.awardDocumentation = this.awardDocumentation;

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getAwardDocumentationAttachments();
      }
    });
  }

  closeModal() {
    this.notifyParent.emit('Reload');
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
