import { Component, OnInit, Input, OnDestroy, OnChanges } from '@angular/core';
import { Award } from 'src/app/shared/models/award.model';
import { AwardCategoryModalFormComponent } from '../award-category-modal-form/award-category-modal-form.component';
import { AwardCategory } from 'src/app/shared/models/award-category.model';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { AwardCategoryService } from 'src/app/shared/services/award-category.service';
import { takeUntil } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { AwardDocumentation } from 'src/app/shared/models/award-documentation.model';
import { AwardDocumentationModalFormComponent } from 'src/app/award-documentation/award-documentation-modal-form/award-documentation-modal-form.component';
import { AwardDocumentationService } from 'src/app/shared/services/award-documentation.service';
import { ToastrService } from 'ngx-toastr';
import { AwardDocumentationAttachmentListComponent } from 'src/app/award-documentation-attachment/award-documentation-attachment-list/award-documentation-attachment-list.component';

@Component({
  selector: 'app-award-category-tab',
  templateUrl: './award-category-tab.component.html',
  styles: []
})
export class AwardCategoryTabComponent implements OnInit, OnDestroy, OnChanges {

  @Input('fromParent') award: Award;

  public awardCategories: AwardCategory[];
  public attachmentCount: number = 0;

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: AwardCategoryService,
    public awardDocumentationService: AwardDocumentationService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    //this.getAwardCategories();
  }

  getAwardCategories() {
    this.service.getAwardCategories(this.award.AwardId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: AwardCategory[]) => {
        this.awardCategories = response;
      });
  }

  showDataEntry(ac?: AwardCategory) {
    let modalRef = this.modal.open(AwardCategoryModalFormComponent, { backdrop: 'static' });

    if (ac != null) {
      modalRef.componentInstance.create = false;
      modalRef.componentInstance.modalData = Object.assign({}, ac);
    } else {
      ac = new AwardCategory();
      ac.AwardCategoryId = 0;
      ac.AwardId = this.award.AwardId;
      ac.AwardCategoryDescription = '';

      modalRef.componentInstance.create = true;
      modalRef.componentInstance.modalData = Object.assign({}, ac);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getAwardCategories();
      }
    });
  }

  onDelete(AwardCategoryId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteAwardCategory(AwardCategoryId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getAwardCategories();
            this.toastr.success('Deleted Successfullly', 'Award Documentation');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showAwardDocumentationDataEntry(awardCategoryId: number, ad?: AwardDocumentation) {
    let modalRef = this.modal.open(AwardDocumentationModalFormComponent);

    if (ad != null) {
      modalRef.componentInstance.modalData = Object.assign({}, ad);
    } else {
      ad = new AwardDocumentation;
      ad.AwardDocumentationId = 0;
      ad.AwardCategoryId = awardCategoryId;
      ad.DocumentTypeId = 0;
      ad.StartDate = null;
      ad.EndDate = null;
      ad.DueDate = null;
      ad.StatusId = 0;

      modalRef.componentInstance.modalData = Object.assign({}, ad);
    }
    modalRef.componentInstance.award = Object.assign({}, this.award);

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getAwardCategories();
      }
    });
  }

  onDeleteAwardDocumentation(AwardDocumentationId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.awardDocumentationService.deleteAwardDocumentation(AwardDocumentationId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getAwardCategories();
            this.toastr.success('Deleted Successfullly', 'Award Documentation');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showAwardDocumentationAttachmentList(ad: AwardDocumentation) {
    let modalRef = this.modal.open(AwardDocumentationAttachmentListComponent, { size: 'lg', backdrop: 'static' });

    if (ad != null) {
      modalRef.componentInstance.awardDocumentation = Object.assign({}, ad);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getAwardCategories();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

  ngOnChanges() {
    this.getAwardCategories();
  }

}
