import { Component, OnInit, OnDestroy } from '@angular/core';
import { AwardTemplate } from 'src/app/shared/models/award-template.model';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { AwardTemplateService } from 'src/app/shared/services/award-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { AwardTemplateModalFormComponent } from '../award-template-modal-form/award-template-modal-form.component';
import { AwardCategoryTemplateListComponent } from 'src/app/award-category-template/award-category-template-list/award-category-template-list.component';

@Component({
  selector: 'app-award-template-list',
  templateUrl: './award-template-list.component.html',
  styles: []
})
export class AwardTemplateListComponent implements OnInit, OnDestroy {

  public awardTemplateArray: AwardTemplate[];
  public awardTemplateArrayObservable: Observable<AwardTemplate[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: AwardTemplateService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getAwardTemplates();
  }

  /**
   * Searches the awardTemplateArray
   * @param: text
   */
  search(text: string): AwardTemplate[] {
    return this.awardTemplateArray.filter(awardTemplates => {
      const term = text.toLowerCase();
      return awardTemplates.AwardTemplateDescription.toLowerCase().includes(term);
    });
  }

  getAwardTemplates() {
    this.service.getAwardTemplates()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: AwardTemplate[]) => {
        this.awardTemplateArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.awardTemplateArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(AwardTemplateId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteAwardTemplate(AwardTemplateId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getAwardTemplates()
            this.toastr.success('Deleted Successfullly', 'Award Template Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(at?: AwardTemplate) {
    let modalRef = this.modal.open(AwardTemplateModalFormComponent);

    if (at != null) {
      modalRef.componentInstance.modalData = Object.assign({}, at);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getAwardTemplates();
      }
    });
  }

  showAwardCategoryTemplate(at: AwardTemplate) {
    let modalRef = this.modal.open(AwardCategoryTemplateListComponent, { size: 'lg', backdrop: 'static' });
    
    modalRef.componentInstance.awardTemplate = Object.assign({}, at);
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
