import { Component, OnInit, EventEmitter, Output, OnDestroy } from '@angular/core';
import { Status } from 'src/app/shared/models/status.model';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { StatusService } from 'src/app/shared/services/status.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil, startWith, map } from 'rxjs/operators';

@Component({
  selector: 'app-status-lookup',
  templateUrl: './status-lookup.component.html',
  styles: []
})
export class StatusLookupComponent implements OnInit, OnDestroy {

  public statusArray: Status[];
  public statusArrayObservable: Observable<Status[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  @Output() notifyParent: EventEmitter<any> = new EventEmitter();


  constructor(
    public service: StatusService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.getStatuses();
  }

  /**
   * Searches the documentTypeArray
   * @param: text
   */
  search(text: string): Status[] {
    return this.statusArray.filter(statuses => {
      const term = text.toLowerCase();
      return statuses.StatusDescription.toLowerCase().includes(term);
    });
  }

  getStatuses() {
    this.service.getStatuses()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Status[]) => {
        this.statusArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.statusArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  select(s: Status) {
    this.notifyParent.emit(s);
    this.activeModal.close();
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }


}
