import { Component, OnInit, Input, EventEmitter, Output, OnDestroy } from '@angular/core';
import { DocumentType } from 'src/app/shared/models/document-type.model';
import { DocumentTypeService } from 'src/app/shared/services/document-type.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { Subject } from 'rxjs';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-document-type-modal-form',
  templateUrl: './document-type-modal-form.component.html',
  styles: []
})
export class DocumentTypeModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: DocumentType;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: DocumentTypeService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        DocumentTypeId: 0,
        DocumentTypeDescription: ''
      }
    }
  }

  onSubmit() {
    if (this.modalData.DocumentTypeId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
    //this.activeModal.close();
  }

  insertRecord(model: DocumentType) {
    this.service.formData = model;
    this.service.postDocumentType()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Created Successfully', 'Document Type Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  updateRecord(model: DocumentType) {
    this.service.formData = model;
    this.service.putDocumentType()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
      res => {
        this.resetForm();
        this.toastr.success('Updated Successfully', 'Document Type Register');
        // send message to parent component via event emitter
        this.notifyParent.emit('Reload');
        this.activeModal.close();
      },
      err => {
        console.log(err);
      }
    )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
