import { Component, OnInit, Output, EventEmitter, OnDestroy } from '@angular/core';
import { DocumentType } from 'src/app/shared/models/document-type.model';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { DocumentTypeService } from 'src/app/shared/services/document-type.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil, startWith, map } from 'rxjs/operators';

@Component({
  selector: 'app-document-type-lookup',
  templateUrl: './document-type-lookup.component.html',
  styles: []
})
export class DocumentTypeLookupComponent implements OnInit, OnDestroy {

  public documentTypeArray: DocumentType[];
  public documentTypeArrayObservable: Observable<DocumentType[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  constructor(
    public service: DocumentTypeService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.getDocumentTypes();
  }

  /**
   * Searches the documentTypeArray
   * @param: text
   */
  search(text: string): DocumentType[] {
    return this.documentTypeArray.filter(documentType => {
      const term = text.toLowerCase();
      return documentType.DocumentTypeDescription.toLowerCase().includes(term);
    });
  }

  getDocumentTypes() {
    this.service.getDocumentTypes()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: DocumentType[]) => {
        this.documentTypeArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.documentTypeArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  select(dt: DocumentType) {
    this.notifyParent.emit(dt);
    this.activeModal.close();
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }
}
