import { Injectable } from '@angular/core';
import { AwardTemplate } from '../models/award-template.model';
import { HttpClient } from '@angular/common/http';
import { AwardTemplateIds } from '../models/award-template-ids.model';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class AwardTemplateService {

  readonly rootURL = this.configService.config['api_root'];

  formData: AwardTemplate;

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postAwardTemplate() {
    return this.http.post(this.rootURL + '/AwardTemplate', this.formData);
  }

  postAwardTemplateBatch(awardTemplateIds: AwardTemplateIds) {
    return this.http.post(this.rootURL + '/AwardTemplate/Batch', awardTemplateIds);
  }

  putAwardTemplate() {
    return this.http.put(this.rootURL + '/AwardTemplate/' + this.formData.AwardTemplateId, this.formData);
  }

  deleteAwardTemplate(id) {
    return this.http.delete(this.rootURL + '/AwardTemplate/' + id);
  }

  getAwardTemplates() {
    return this.http.get(this.rootURL + '/AwardTemplate');
  }

  getAwardTemplatesFiltered() {
    return this.http.get(this.rootURL + '/AwardTemplate/filter');
  }

  getAwardTemplateById(id) {
    return this.http.get(this.rootURL + '/AwardTemplate/filter/' + id);
  }
}
