import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { SubgranteeType } from 'src/app/shared/models/subgrantee-type.model';
import { SubgranteeTypeService } from 'src/app/shared/services/subgrantee-type.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal, NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { FormControl } from '@angular/forms';
import { takeUntil, startWith, map } from 'rxjs/operators';
import { SubgranteeTypeModalFormComponent } from '../subgrantee-type-modal-form/subgrantee-type-modal-form.component';

@Component({
  selector: 'app-subgrantee-type-list',
  templateUrl: './subgrantee-type-list.component.html',
  styles: []
})
export class SubgranteeTypeListComponent implements OnInit, OnDestroy {
    

  public subGranteeTypeArray: SubgranteeType[];
  public subGranteeTypeArrayObservable: Observable<SubgranteeType[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeTypeService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getSubGranteeTypes();
  }

  /**
   * Searches the donorTypeArray
   * @param: text
   */
  search(text: string): SubgranteeType[] {
    return this.subGranteeTypeArray.filter(subGranteeTypes => {
      const term = text.toLowerCase();
      return subGranteeTypes.SubGranteeTypeDescription.toLowerCase().includes(term);
    });
  }

  getSubGranteeTypes() {
    this.service.getSubGranteeTypes()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: SubgranteeType[]) => {
        this.subGranteeTypeArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.subGranteeTypeArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(SubgranteeTypeId, deleteModal) {

    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteSubGranteeType(SubgranteeTypeId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getSubGranteeTypes();
            this.toastr.success('Deleted Successfullly', 'Sub Grantee Type Register');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(sgt?: SubgranteeType) {
    let modalRef = this.modal.open(SubgranteeTypeModalFormComponent);

    if (sgt != null) {
      modalRef.componentInstance.modalData = Object.assign({}, sgt);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getSubGranteeTypes();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
