import { Injectable } from '@angular/core';
import { SubgranteeType } from '../models/subgrantee-type.model';
import { HttpClient } from '@angular/common/http';
import { AppConfig } from 'src/app/app-config';

@Injectable({
  providedIn: 'root'
})
export class SubgranteeTypeService {
  readonly rootURL = this.configService.config['api_root'];

  formData: SubgranteeType;
  list: SubgranteeType[];

  constructor(public http: HttpClient, public configService: AppConfig) { }

  postSubGranteeType() {
    return this.http.post(this.rootURL + '/SubGranteeType', this.formData);
  }

  putSubGranteeType() {
    return this.http.put(this.rootURL + '/SubGranteeType/' + this.formData.SubGranteeTypeId, this.formData);
  }

  deleteSubGranteeType(id) {
    return this.http.delete(this.rootURL + '/SubGranteeType/' + id);
  }

  getSubGranteeTypes() {
    return this.http.get(this.rootURL + '/SubGranteeType');
  }
}
