import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { SubgranteeDocumentation } from 'src/app/shared/models/subgrantee-documentation.model';
import { Subject } from 'rxjs';
import { SubgranteeDocumentationAttachmentService } from 'src/app/shared/services/subgrantee-documentation-attachment.service';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { ToastrService } from 'ngx-toastr';
import { takeUntil } from 'rxjs/operators';
import { HttpEventType } from '@angular/common/http';

@Component({
  selector: 'app-subgrantee-documentation-attachment-modal-form',
  templateUrl: './subgrantee-documentation-attachment-modal-form.component.html',
  styles: []
})
export class SubgranteeDocumentationAttachmentModalFormComponent implements OnInit, OnDestroy {

  @Input() subgranteeDocumentation: SubgranteeDocumentation;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public progress: number;
  public message: string = '';
  public showDiv: boolean = false;

  constructor(
    public service: SubgranteeDocumentationAttachmentService,
    public activeModal: NgbActiveModal,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
  }

  /**
   * Subscribes to the observable method (upload) from SubGranteeDocumentationAttachmentService
   * @param: files (from the fileupload control)
   * */
  upload(files) {
    this.service.upload(files, this.subgranteeDocumentation.SubGranteeDocumentationId)
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(event => {

        if (event.type === HttpEventType.UploadProgress)
          this.progress = Math.round(100 * event.loaded / event.total);
        else if (event.type === HttpEventType.Response)
          this.message = event.body.toString();

        // Notify parent to refresh the grid
        this.notifyParent.emit('Reload');

      }, error => {
        console.error(error);
      }, () => {
        this.activeModal.close();
        this.toastr.success("File successfully uploaded!", "File Upload");
      }
      )
  }

  /**
   * Closes the active modal
   * @param: None
   * */
  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
