import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { Subgrantee } from 'src/app/shared/models/subgrantee.model';
import { Subject } from 'rxjs';
import { SubgranteeService } from 'src/app/shared/services/subgrantee.service';
import { SubgranteeTypeService } from 'src/app/shared/services/subgrantee-type.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { SubgranteeType } from 'src/app/shared/models/subgrantee-type.model';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-subgrantee-modal-form',
  templateUrl: './subgrantee-modal-form.component.html',
  styles: []
})
export class SubgranteeModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: Subgrantee;
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  public subGranteeTypes: SubgranteeType[];

  constructor(
    public service: SubgranteeService,
    public subGranteeTypeService: SubgranteeTypeService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }

  ngOnInit() {
    this.resetForm();
    this.getSubGranteeTypes();
  }

  getSubGranteeTypes() {
    this.subGranteeTypeService.getSubGranteeTypes()
      .subscribe((response: SubgranteeType[]) => {
        this.subGranteeTypes = response;
      });
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        SubGranteeId: 0,
        SubGranteeTypeId: 0,
        SubGranteeName: '',
        SubGranteeTypeDescription: ''
      }
    }
  }

  onSubmit() {
    if (this.modalData.SubGranteeId == 0) {
      this.insertRecord(this.modalData)
    } else {
      this.updateRecord(this.modalData);
    }
  }

  insertRecord(model: Subgrantee) {
    this.service.formData = model;
    this.service.postSubGrantee()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Sub Grantee');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(model: Subgrantee) {
    this.service.formData = model;
    this.service.putSubGrantee()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm()
          this.toastr.success('Updated Successfully', 'Sub Grantee');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
