import { Component, OnInit, Input, Output, EventEmitter, OnDestroy } from '@angular/core';
import { SubgranteeTemplate } from 'src/app/shared/models/subgrantee-template.model';
import { Subject } from 'rxjs';
import { SubgranteeTemplateService } from 'src/app/shared/services/subgrantee-template.service';
import { ToastrService } from 'ngx-toastr';
import { NgbActiveModal } from '@ng-bootstrap/ng-bootstrap';
import { takeUntil } from 'rxjs/operators';

@Component({
  selector: 'app-subgrantee-template-modal-form',
  templateUrl: './subgrantee-template-modal-form.component.html',
  styles: []
})
export class SubgranteeTemplateModalFormComponent implements OnInit, OnDestroy {

  @Input() modalData: SubgranteeTemplate
  @Output() notifyParent: EventEmitter<any> = new EventEmitter();

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: SubgranteeTemplateService,
    public toastr: ToastrService,
    public activeModal: NgbActiveModal
  ) { }
  
  ngOnInit() {
    this.resetForm();
  }

  resetForm() {
    if (this.modalData == null) {
      this.modalData = {
        SubGranteeTemplateId: 0,
        SubGranteeTemplateDescription: '',
        IsDefault: false,
        OpportunitySubGranteeId: 0,
        SubGranteeCategoryTemplates: []
      }
    }
  }

  onSubmit() {
    if (this.modalData.SubGranteeTemplateId == 0) {
      this.insertRecord(this.modalData);
    } else {
      this.updateRecord(this.modalData);
    }
    //this.activeModal.close();
  }

  insertRecord(model: SubgranteeTemplate) {
    this.service.formData = model;
    this.service.postSubGranteeTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Created Successfully', 'Sub Grantee Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  updateRecord(model: SubgranteeTemplate) {
    this.service.formData = model;
    this.service.putSubGranteeTemplate()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe(
        res => {
          this.resetForm();
          this.toastr.success('Updated Successfully', 'Sub Grantee Template Register');
          // send message to parent component via event emitter
          this.notifyParent.emit('Reload');
          this.activeModal.close();
        },
        err => {
          console.log(err);
        }
      )
  }

  closeModal() {
    this.activeModal.close();
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
