import { Component, OnInit, OnDestroy } from '@angular/core';
import { OpportunityService } from 'src/app/shared/services/opportunity.service';
import { ToastrService } from 'ngx-toastr';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Opportunity } from 'src/app/shared/models/opportunity.model';
import { OpportunityModalFormComponent } from '../opportunity-modal-form/opportunity-modal-form.component';
import { Observable, Subject } from 'rxjs';
import { FormControl } from '@angular/forms';
import { takeUntil, startWith, map } from 'rxjs/operators';

@Component({
  selector: 'app-opportunity-list',
  templateUrl: './opportunity-list.component.html',
  styles: []
})
export class OpportunityListComponent implements OnInit, OnDestroy {

  public opportunityTypeArray: Opportunity[];
  public opportunityArrayObservable: Observable<Opportunity[]>;
  public filter = new FormControl('');

  public ngUnsubscribe: Subject<any> = new Subject();

  constructor(
    public service: OpportunityService,
    public toastr: ToastrService,
    public modal: NgbModal
  ) { }

  ngOnInit() {
    this.getOpportunities();
  }

  /**
   * Searches the documentTypeArray
   * @param: text
   */
  search(text: string): Opportunity[] {
    return this.opportunityTypeArray.filter(opportunities => {
      const term = text.toLowerCase();
      return  opportunities.RFPName.toLowerCase().includes(term) ||
              opportunities.RFPId.toString().includes(term) ||
              opportunities.AwardNumber.toLowerCase().includes(term);
    });
  }

  getOpportunities() {
    this.service.getOpportunities()
      .pipe(takeUntil(this.ngUnsubscribe))
      .subscribe((response: Opportunity[]) => {
        this.opportunityTypeArray = response;
      }, err => {
        console.log(err);
      },
        () => {
          this.opportunityArrayObservable = this.filter.valueChanges.pipe(
            startWith(''),
            map(text => this.search(text))
          );
        });
  }

  onDelete(RFPId, deleteModal) {
    this.modal.open(deleteModal).result.then(result => {
      this.service.deleteOpportunity(RFPId)
        .pipe(takeUntil(this.ngUnsubscribe))
        .subscribe(
          res => {
            this.getOpportunities();
            this.toastr.success('Deleted Successfullly', 'Opportunity Registration');
          },
          err => {
            console.log(err)
          }
        )
    }, reason => console.log(`Dismissed: ${reason}`));
  }

  showDataEntry(o?: Opportunity) {
    let modalRef = this.modal.open(OpportunityModalFormComponent);

    if (o != null) {
      modalRef.componentInstance.modalData = Object.assign({}, o);
    }

    // Subscribe to the modal event emitter and wait for the response
    modalRef.componentInstance.notifyParent.subscribe(response => {
      if (response == 'Reload') {
        this.getOpportunities();
      }
    });
  }

  ngOnDestroy() {
    this.ngUnsubscribe.next();
    this.ngUnsubscribe.complete();
  }

}
